import * as THREE from 'three';

export class Water {
    constructor(scene, poolDimensions, waterDimensions, position = new THREE.Vector3(0, 0, 0)) {
        this.scene = scene;
        this.poolDimensions = poolDimensions;
        this.waterDimensions = waterDimensions || {
            width: poolDimensions.width,
            height: 0.1 / (poolDimensions.width * poolDimensions.depth),
            depth: poolDimensions.depth
        };
        this.position = position;
        this.waterDensity = 1000;
        this.gravity = 9.81;
        this.objectsInWater = new Map();
        this.totalDisplacedVolume = 0;

        const waterArea = this.waterDimensions.width * this.waterDimensions.depth;
        this.baseWaterHeight = this.waterDimensions.height;
        this.currentWaterHeight = this.baseWaterHeight;

        // Calculate the actual volume with given dimensions
        this.actualVolume = this.waterDimensions.width * this.baseWaterHeight * this.waterDimensions.depth; // 0.192 m³
        this.targetVolume = 0.1; // 100 liters = 0.1 m³
        this.volumeScaleFactor = this.targetVolume / this.actualVolume; // 0.1 / 0.192 ≈ 0.520833

        // Adjust the current water height to represent 100 liters
        this.currentWaterHeight = this.baseWaterHeight * this.volumeScaleFactor; // 0.4 * 0.520833 ≈ 0.208333 m
        this.effectiveWidth = this.waterDimensions.width;
        this.effectiveDepth = this.waterDimensions.depth;

        this.waterLevel = this.currentWaterHeight / this.poolDimensions.height;

        this.maxWaterHeight = 0.4583; // Restrict maximum water height

        this.createWaterMesh();
    }

    createWaterMesh() {
        const geometry = new THREE.BoxGeometry(
            this.waterDimensions.width,
            this.currentWaterHeight,
            this.waterDimensions.depth
        );
        const material = new THREE.MeshStandardMaterial({
            color: 0x00aaff,
            transparent: true,
            opacity: 0.6,
            metalness: 0.1,
            roughness: 0.1,
            side: THREE.DoubleSide,
            envMap: this.scene.environment,
            envMapIntensity: 1.0,
        });

        this.waterMesh = new THREE.Mesh(geometry, material);
        this.waterMesh.position.set(
            this.position.x,
            this.position.y + (this.currentWaterHeight / 2),
            this.position.z
        );
        this.scene.add(this.waterMesh);
    }

    // Method to increase water height while maintaining volume
    increaseWaterHeight(newHeight) {
        // Restrict the new height to the maximum allowed
        newHeight = Math.min(newHeight, this.maxWaterHeight);
        if (newHeight <= this.baseWaterHeight * this.volumeScaleFactor) return;

        // Calculate the new effective area to maintain volume
        const currentVolume = this.targetVolume + this.totalDisplacedVolume; // In m³
        const newArea = currentVolume / newHeight; // width * depth
        const aspectRatio = this.waterDimensions.width / this.waterDimensions.depth; // 1.2 / 0.4 = 3

        // Solve for new effective width and depth
        this.effectiveDepth = Math.sqrt(newArea / aspectRatio);
        this.effectiveWidth = aspectRatio * this.effectiveDepth;

        this.currentWaterHeight = newHeight;

        // Update the water mesh geometry (keep visual dimensions, but height changes)
        this.waterMesh.geometry.dispose();
        this.waterMesh.geometry = new THREE.BoxGeometry(
            this.waterDimensions.width,
            this.currentWaterHeight,
            this.waterDimensions.depth
        );
        this.waterMesh.position.y = this.position.y + (this.currentWaterHeight / 2);
        this.waterLevel = this.currentWaterHeight / this.poolDimensions.height;
    }

    addObject(object) {
        if (!object.userData.interactWithWater) return;

        this.objectsInWater.set(object, {
            mass: object.userData.mass || 1,
            volume: object.userData.volume || 0.001,
        });

        object.userData.velocity = object.userData.velocity || new THREE.Vector3();
        object.userData.inWater = object.userData.inWater || false;
        object.userData.isBeingDragged = object.userData.isBeingDragged || false;
    }

    removeObject(object) {
        this.objectsInWater.delete(object);
    }

    adjustWaterLevel() {
        this.totalDisplacedVolume = 0;
        this.objectsInWater.forEach((data, object) => {
            if (object.userData.inWater) {
                const submergedVolume = this.getSubmergedVolume(object);
                this.totalDisplacedVolume += submergedVolume;
            }
        });

        // Adjust water height based on displaced volume, using effective dimensions
        const effectiveArea = this.effectiveWidth * this.effectiveDepth;
        const waterLevelIncrease = this.totalDisplacedVolume / effectiveArea;
        const newWaterHeight = (this.targetVolume / effectiveArea) + waterLevelIncrease;

        // Ensure the new water height does not exceed the maximum
        if (newWaterHeight > this.maxWaterHeight) {
            this.currentWaterHeight = this.maxWaterHeight;
            // Recalculate effective dimensions to accommodate the displaced volume at max height
            const newAreaAtMaxHeight = (this.targetVolume + this.totalDisplacedVolume) / this.maxWaterHeight;
            const aspectRatio = this.waterDimensions.width / this.waterDimensions.depth;
            this.effectiveDepth = Math.sqrt(newAreaAtMaxHeight / aspectRatio);
            this.effectiveWidth = aspectRatio * this.effectiveDepth;
        } else {
            this.currentWaterHeight = newWaterHeight;
        }

        this.waterMesh.geometry.dispose();
        this.waterMesh.geometry = new THREE.BoxGeometry(
            this.waterDimensions.width,
            this.currentWaterHeight,
            this.waterDimensions.depth
        );
        this.waterMesh.position.y = this.position.y + (this.currentWaterHeight / 2);
        this.waterLevel = this.currentWaterHeight / this.poolDimensions.height;
    }

    getSubmergedVolume(object) {
        const objectBox = new THREE.Box3().setFromObject(object);
        const objectHeight = objectBox.max.y - objectBox.min.y;
        const waterSurfaceY = this.position.y + this.currentWaterHeight;

        if (objectBox.min.y >= waterSurfaceY) return 0;
        if (objectBox.max.y <= waterSurfaceY) return object.userData.volume;

        const submergedHeight = waterSurfaceY - objectBox.min.y;
        return object.userData.volume * (submergedHeight / objectHeight);
    }

    update(deltaTime) {
        const waterBox = new THREE.Box3().setFromObject(this.waterMesh);

        this.objectsInWater.forEach((data, object) => {
            if (object.userData.isBeingDragged) {
                object.userData.velocity.y = 0;
                return;
            }

            const { mass, volume } = data;
            const density = mass / volume;
            const objectBox = new THREE.Box3().setFromObject(object);
            const objectHeight = objectBox.max.y - objectBox.min.y;
            const waterSurfaceY = this.position.y + this.currentWaterHeight;

            object.userData.inWater = objectBox.intersectsBox(waterBox);

            if (object.userData.inWater) {
                const submergedVolume = this.getSubmergedVolume(object);
                const buoyantForce = this.waterDensity * this.gravity * submergedVolume;
                const weight = mass * this.gravity;
                const netForce = buoyantForce - weight;
                const acceleration = netForce / mass;

                object.userData.velocity.y += acceleration * deltaTime;
                object.userData.velocity.y *= 0.98;

                if (density < this.waterDensity) {
                    const submergedFraction = submergedVolume / volume;
                    const equilibriumHeight = waterSurfaceY - (objectHeight * submergedFraction) + objectHeight / 2;
                    const distanceToEquilibrium = equilibriumHeight - object.position.y;
                    object.userData.velocity.y += distanceToEquilibrium * 0.05;
                    object.userData.velocity.y = Math.max(Math.min(object.userData.velocity.y, 0.3), -0.3);
                }
            } else {
                object.userData.velocity.y -= this.gravity * deltaTime;
            }

            object.position.y += object.userData.velocity.y * deltaTime;
        });

        this.adjustWaterLevel();
    }

    dispose() {
        this.scene.remove(this.waterMesh);
        this.waterMesh.geometry.dispose();
        this.waterMesh.material.dispose();
        this.objectsInWater.clear();
    }

    // Method to get the effective volume for external use (e.g., volume indicator)
    getEffectiveVolume() {
        return (this.targetVolume + this.totalDisplacedVolume) * 1000; // Convert to liters
    }
}