import React from 'react';
import { useNavigate } from 'react-router-dom';

const MainHomePage = () => {
  const navigate = useNavigate();

  const handleExperiment1Click = () => {
    navigate('/experiment1');
  };

  const handleExperiment2Click = () => {
    navigate('/experiment2');
  };

  const handleExperiment3Click = () => {
    navigate('/experiment3');
  };

  const handleExperiment4Click = () => {
    navigate('/experiment4');
  };

  const handleExperiment5Click = () => {
    navigate('/experiment5');
  };

  const handleExperiment6Click = () => {
    navigate('/experiment6');
  };


  return (
    <div className="homepage-container">
      <header className="homepage-header">
        <h1>Chemistry Experiment Simulator</h1>
        <p>Explore interactive 3D simulations of classic chemistry experiments</p>
      </header>

      <main className="experiment-selection">

        {/* Experiment 1 */}
        <div className="experiment-card" onClick={handleExperiment1Click}>
          <h2>Experiment 1</h2>
          <p>Separation of Components of a Mixture</p>
          <br></br>
          <button className="explore-button">Start Experiment</button>
        </div>

        {/* Experiment 2 */}
        <div className="experiment-card" onClick={handleExperiment2Click}>
          <h2>Experiment 2</h2>
          <p>Flame Test</p>
          <br></br>
          <button className="explore-button">Start Experiment</button>
        </div>

        {/* Experiment 3 */}
        <div className="experiment-card" onClick={handleExperiment3Click}>
          <h2>Experiment 3</h2>
          <p>Archimedes' principle </p>
          <br></br>
          <button className="explore-button">Start Experiment</button>
        </div>

        {/* Experiment 4 */}
        <div className="experiment-card" onClick={handleExperiment4Click}>
          <h2>Experiment 4</h2>
          <p>Preparation of Oxides, Hydroxides and Oxyacids of Magnesium and Sulphur</p>
          <br></br>
          <button className="explore-button">Start Experiment</button>
        </div>

        {/* Experiment 5 */}
        <div className="experiment-card" onClick={handleExperiment5Click}>
          <h2>Experiment 5</h2>
          <p>Mixture of some anions and cations that produce a precipitate</p>
          <br></br>
          <button className="explore-button">Start Experiment</button>
        </div>

        {/* Experiment 6 */}
        <div className="experiment-card" onClick={handleExperiment6Click}>
          <h2>Experiment 6</h2>
          <p>Reactions </p>
          <br></br>
          <button className="explore-button">Start Experiment</button>
        </div>

        
        {/* Experiment 7 */}
        <div className="experiment-card" onClick={handleExperiment2Click}>
          <h2>Experiment 7</h2>
          <p>Archimedes' principle </p>
          <br></br>
          <button className="explore-button">Start Experiment</button>
        </div>

        {/* Experiment 8 */}
        <div className="experiment-card" onClick={handleExperiment1Click}>
          <h2>Experiment 8</h2>
          <p>Separation of Components of a Mixture</p>
          <br></br>
          <button className="explore-button">Start Experiment</button>
        </div>

        {/* Experiment 9 */}
        <div className="experiment-card" onClick={handleExperiment2Click}>
          <h2>Experiment 9</h2>
          <p>Flame Test</p>
          <br></br>
          <button className="explore-button">Start Experiment</button>
        </div>

        {/* Experiment 10 */}
        <div className="experiment-card" onClick={handleExperiment2Click}>
          <h2>Experiment 10</h2>
          <p>Archimedes' principle </p>
          <br></br>
          <button className="explore-button">Start Experiment</button>
        </div>

      </main>

      <footer className="homepage-footer">
        <p> © 2025 Chemistry Lab</p>
      </footer>
    </div>
  );
};

export default MainHomePage;