import React, { useEffect, useState } from 'react';
import './custom-stepper.css'

const ChemistryLabStepper = ({ currentState, onStateChange, activeState, types, process }) => {

  // console.log("activeState : ", activeState);
  console.log("Type : ", types);

  const [type, setType] = useState(types)
  const [activeProcess, setActiveProcess] = useState(process);
  const [activeStep, setActiveStep] = useState(0);

  // Object mapping component states to stepper processes
  const stateToProcess = {
    step1_exp1: 'sublimation',
    sublimation: 'sublimation',
    naptheline: 'sublimation',
    filterationStage: 'filtration',
    sandSeparation: 'filtration',
    evaporation: 'evaporation'
  };



  // Object mapping component states to specific steps within processes
  const stateToStep = {
    step1_exp1: 0,
    sublimation: 1,
    naptheline: 2,
    filterationStage: 0,
    sandSeparation: 2,
    // Add evaporation steps when needed
  };

  useEffect(() => {
    // Update active process and step based on current state
    if (currentState) {
      const process = stateToProcess[currentState];
      if (process) {
        setActiveProcess(process);
        setActiveStep(stateToStep[currentState] || 0);
      }
    }


  }, [currentState]);


  useEffect(() => {
    if (activeState === 'sublimation') {
      handleProcessChange('sublimation')
    } else if (activeState === 'filtration') {

    } else if (activeState === 'evaporation') {

    }

  }, [activeState])

  const separationProcesses = {
    sublimation: {
      title: "Sublimation",
      description: "To Separate naphthalene from the mixture",
      steps: [
        "Drag bowl & place it on Burner",
        "Drag the funnel & place it on bowl which contains the mixture",
        "Click on sublimation button to start sublimation process & wait for few seconds",
        "Drag/click on spoon & wait for few seconds which will separate the naphthalene from mixture"
      ],
      conclusion: "Naphthalin is separated from the process of Sublimation",
      buttonId: "sublimationButton"
    },
    filtration: {
      title: "Filtration",
      description: "To Separate sand from the mixture",
      steps: [
        "Grab the bowl towards the beaker with spoon & wait for few seconds",
        "Grab the beaker & towards funnel & wait for few seconds which will filters the sand & mixture",
        "Drag/click on spoon & wait for few seconds which will extract the sand from funnel"
      ],
      conclusion: "Sand is separated by the process of Filtration",
      buttonId: "filterationButton"
    },
    evaporation: {
      title: "Evaporation Stage",
      description: "To Separate salt from mixture",
      steps: [
        "Grab the beaker towards bowl on burner for evaporation",
        "Wait for few seconds",
        "Grab/click on spoon & wait for few seconds which will extract the salt"
      ],
      conclusion: "Salt is separated by the process of Evaporation",
      buttonId: "evaporationButton"
    }
  };

  const metalOxideSeparationProcess = {
    magnesium: {
      title: "Magnesium",
      description: "To demonstrate the formation of magnesium oxide",
      steps: [
        "Burn this Magnesium ribbon under bunsen burner flame and obtain ash residue. Its called magnesium oxide.",
        "Take that residue, put that in test tube, and add water in it and shake it. (Shake animation) Hence, Magnesium hydroxide is formed",
        "Add two drops of phenolphthalein in that test tube solution and the color of the solution turns PINK. Hence, its a basic solution in nature.",
      ],
      conclusion: "The solution turns PINK. Hence, its a basic solution in nature.",
      buttonId: "magnesiumButton"
    },
    sulphur: {
      title: "Sulphur",
      description: "To demonstrate the formation of sulphur dioxide (SO₂) gas",
      steps: [
        "Take a piece of sulphur on a spoon and heat it on bunsen burner flame, and some smelly gas forms (gas animation). Hence Sulphur dioxide (SO₂) an acidic oxide is formed.",
        "Trap that gas under Erlenmeyer flask (pic is given below), add water in it and close it. After that shake well the flask. Hence sulfurous acid (H₂SO₃) is formed. (Ithe animation or simulation cha thoda vichar karava laagel)",
        "Add few drops of phelphthalein in it to check whether the solution is acidic."
      ],
      conclusion: "The colour stays colourless that's why it is acidic in nature.",
      buttonId: "sulphurButton"
    },

  };

  const handleProcessChange = (process) => {
    setActiveProcess(process);
    setActiveStep(0);

    // Call the onStateChange callback with the appropriate state
    if (onStateChange) {
      // Map from process to the appropriate state
      const stateMap = {
        'sublimation': 'step1_exp1',
        'filtration': 'filterationStage',
        'evaporation': 'evaporation'
      };
      onStateChange(stateMap[process]);
    }
  };


  return (
    <div className="chemistry-lab-container">
      <div className="process-selector">
        {type === "exp1" ? Object.keys(separationProcesses).map((process) => (
          <button
            key={process}
            onClick={() => handleProcessChange(process)}
            className={`btmSumblimation  ${activeProcess === process ? 'glow-effect' : ''}`}
            id={separationProcesses[process].buttonId}
          >
            {process.charAt(0).toUpperCase() + process.slice(1)}
          </button>
        ))
          :
          Object.keys(metalOxideSeparationProcess).map((process) => (
            <button
              key={process}
              onClick={() => handleProcessChange(process)}
              className={`btmSumblimation  ${activeProcess === process ? 'glow-effect' : ''}`}
              id={metalOxideSeparationProcess[process].buttonId}
            >
              {process.charAt(0).toUpperCase() + process.slice(1)}
            </button>
          ))
        }
      </div>

      <div className="process-details">
        <div className="process-header">
          <div className="process-number">
            {type === "exp1" ? Object.keys(separationProcesses).indexOf(activeProcess) + 1 : Object.keys(metalOxideSeparationProcess).indexOf(activeProcess) + 1}
          </div>
          {type === "exp1" ? <div>
            <h2 className="process-title">{separationProcesses[activeProcess]?.title}</h2>
            <p className="process-description">{separationProcesses[activeProcess]?.description}</p>
          </div>
          :
          <div>
            <h2 className="process-title">{metalOxideSeparationProcess[activeProcess]?.title}</h2>
            <p className="process-description">{metalOxideSeparationProcess[activeProcess]?.description}</p>
          </div>
}
        </div>

        <div className="process-steps">
          {type === "exp1" ? separationProcesses[activeProcess].steps.map((step, index) => (
            <div key={index} className={`step ${activeStep === index ? 'active-step' : ''}`}>
              {/* <div className={`step-number ${index <= activeStep ? 'completed' : ''}`}>
                {index + 1}
              </div> */}
              <p className={`step-text ${activeStep === index ? 'highlighted' : ''}`}>{step}</p>
            </div>
          ))
            :
            metalOxideSeparationProcess[activeProcess]?.steps.map((step, index) => (
              <div key={index} className={`step ${activeStep === index ? 'active-step' : ''}`}>
                {/* <div className={`step-number ${index <= activeStep ? 'completed' : ''}`}>
              {index + 1}
            </div> */}
                <p className={`step-text ${activeStep === index ? 'highlighted' : ''}`}>{step}</p>
              </div>
            ))
          }

          <div className="process-conclusion">
            <p className="">{type === "exp1" ? separationProcesses[activeProcess]?.conclusion : metalOxideSeparationProcess[activeProcess]?.conclusion}</p>
          </div>
        </div>
      </div>
    </div>
  );
};

export default ChemistryLabStepper;