import React, { Component } from 'react';
import * as THREE from 'three';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { CSS2DRenderer, CSS2DObject } from 'three/examples/jsm/renderers/CSS2DRenderer.js';
import { Water } from './Water';

class ArchemedisePrincipalComponent extends Component {
    constructor(props) {
        super(props);
        this.scene = null;
        this.camera = null;
        this.renderer = null;
        this.labelRenderer = null;
        this.cube = null;
        this.frameId = null;
        this.controls = null;
        this.gltfLoader = null;
        this.WNDSIZE = { width: 0, height: 0 };

        this.isDragging = false;
        this.dragObject = null;
        this.dragPlane = null;
        this.initialDragOffset = new THREE.Vector3();

        this.raycaster = null;
        this.pointer = null;
        this.moveMouse = new THREE.Vector2();

        this.water = null;
        this.lastTime = performance.now();
        this.expModel = null;
        this.expModel_left = null;
        this.expModel_right = null;

        this.collisionObjects = [];
        this.cubeBoundingBoxHelper = null;
        this.areBoundingBoxesVisible = true;

        this.waterVolumeLabel = null;
        this.waterVolumeArrow = null;

        this.state = {
            selectedMaterial: 'wood',
            mass: 0.50,
            volume: 0.001,
            waterHeight: 0.208333, // Initial effective height for 100L
        };

        this.materialDensities = {
            wood: 700,
            pvc: 1380,
            aluminum: 2700,
        };

        this.materialAppearances = {
            wood: { color: 0x8B4513, metalness: 0.0, roughness: 0.8, map: "/Resources/Practical3-ArchemedieseP/wood.jpg" },
            pvc: { color: 0x808080, metalness: 0.2, roughness: 0.8, map: "/Resources/Practical3-ArchemedieseP/pvc.jpg" },
            aluminum: { color: 0xC0C0C0, metalness: 0.9, roughness: 0.2,  map: "/Resources/Practical3-ArchemedieseP/al.jpg" },
        };

        this.massRanges = {
            wood: { min: 0.50, max: 4.0 },
            pvc: { min: 1.44, max: 14.40 },
            aluminum: { min: 4.0, max: 27.0 },
        };

        this.volumeRange = { min: 0.001, max: 0.010 };
    }

    componentDidMount() {
        this.initialize();
    }

    componentWillUnmount() {
        this.stop();
        if (this.water) this.water.dispose();
        window.removeEventListener('keydown', this.handleKeyDown);
        window.removeEventListener('mousedown', this.onPointerClick);
        window.removeEventListener('mouseup', this.onPointerUp);
        window.removeEventListener('mousemove', this.onMouseMove);
        window.removeEventListener("resize", this.resizeWindow);
    }

    onPointerUp = () => {
        this.isDragging = false;
        document.body.style.cursor = "default";
        if (this.dragObject) {
            this.dragObject.userData.isBeingDragged = false;
            this.dragObject = null;
        }
        console.log("-----",this.controls);
        
    };

    onPointerClick = (event) => {
        this.pointer.x = (event.clientX / window.innerWidth) * 2 - 1;
        this.pointer.y = -(event.clientY / window.innerHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.pointer, this.camera);

        const draggableObjects = [];
        this.scene.traverse((child) => {
            if (child.userData.draggable) {
                draggableObjects.push(child);
            }
        });

        const intersects = this.raycaster.intersectObjects(draggableObjects, true);

        if (intersects.length > 0) {
            this.dragObject = intersects[0].object;
            this.dragObject.userData.isBeingDragged = true;
            this.isDragging = true;

            const worldPos = this.dragObject.position.clone();
            if (this.dragObject.parent) {
                this.dragObject.parent.localToWorld(worldPos);
            }
            this.dragPlane.position.copy(worldPos);
            this.dragPlane.lookAt(this.camera.position);

            const planeIntersects = this.raycaster.intersectObject(this.dragPlane);
            if (planeIntersects.length > 0) {
                this.initialDragOffset.subVectors(worldPos, planeIntersects[0].point);
            } else {
                this.initialDragOffset.set(0, 0, 0);
            }
        }
    }

    onMouseMove = (event) => {
        this.moveMouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        this.moveMouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.moveMouse, this.camera);

        const draggableObjects = [];
        this.scene.traverse((child) => {
            if (child.userData.draggable) {
                draggableObjects.push(child);
            }
        });
        const intersects = this.raycaster.intersectObjects(draggableObjects, true);

        document.body.style.cursor = intersects.length > 0 ? "pointer" : "default";

        if (this.isDragging && this.dragObject) {
            const intersects = this.raycaster.intersectObject(this.dragPlane);
            if (intersects.length > 0) {
                const intersection = intersects[0].point;
                const newWorldPos = intersection.add(this.initialDragOffset);

                let localPos = newWorldPos.clone();
                if (this.dragObject.parent) {
                    this.dragObject.parent.worldToLocal(localPos);
                }
                this.dragObject.position.x = localPos.x;
                this.dragObject.position.y = localPos.y;
            }
        }
    }

    initialize = () => {
        this.scene = new THREE.Scene();
        this.WNDSIZE.width = this.mount.clientWidth;
        this.WNDSIZE.height = this.mount.clientHeight;

        this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
        this.renderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
        this.renderer.toneMapping = THREE.ACESFilmicToneMapping;
        this.renderer.toneMappingExposure = 0.5;
        this.renderer.shadowMap.enabled = true;
        this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
        this.renderer.outputEncoding = THREE.sRGBEncoding;

        this.labelRenderer = new CSS2DRenderer();
        this.labelRenderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
        this.labelRenderer.domElement.style.position = 'absolute';
        this.labelRenderer.domElement.style.top = '0px';
        this.labelRenderer.domElement.style.pointerEvents = 'none';
        this.mount.appendChild(this.labelRenderer.domElement);

        const poolDimensions = { width: 1.2, height: 0.7, depth: 0.4 };
        const waterDimensions = { width: 1.2, height: 0.4, depth: 0.4 }; // As per your setting
        const waterPosition = new THREE.Vector3(0.05, -0.19, -0.08);
        this.water = new Water(this.scene, poolDimensions, waterDimensions, waterPosition);

        this.mount.appendChild(this.renderer.domElement);

        this.raycaster = new THREE.Raycaster();
        this.pointer = new THREE.Vector2();
        this.gltfLoader = new GLTFLoader();

        this.setupEnvMap();
        this.setupModel();
        this.setupLight();
        this.createDragPlane();
        this.setupCamera();
        this.setupWaterVolumeIndicator();

        this.start();

        window.addEventListener('mousedown', this.onPointerClick);
        window.addEventListener('mouseup', this.onPointerUp);
        window.addEventListener('mousemove', this.onMouseMove);
        window.addEventListener("resize", this.resizeWindow);
        window.addEventListener('keydown', this.handleKeyDown);
    }

    setupWaterVolumeIndicator = () => {
        const labelDiv = document.createElement('div');
        labelDiv.className = 'water-volume-label';
        labelDiv.style.backgroundColor = 'rgba(255, 255, 255, 0.8)';
        labelDiv.style.padding = '5px';
        labelDiv.style.borderRadius = '5px';
        labelDiv.style.fontSize = '14px';
        labelDiv.style.fontFamily = 'Arial, sans-serif';
        labelDiv.style.color = 'black';
        labelDiv.textContent = '100.00 L'; // Initial value based on target volume

        this.waterVolumeLabel = new CSS2DObject(labelDiv);
        this.waterVolumeLabel.position.set(
            this.water.position.x + this.water.waterDimensions.width / 2 + 0.01,
            this.water.position.y + this.water.currentWaterHeight,
            this.water.position.z + 0.25
        );
        this.scene.add(this.waterVolumeLabel);

        const arrowGeometry = new THREE.ConeGeometry(0.02, 0.05, 32);
        const arrowMaterial = new THREE.MeshBasicMaterial({ color: 0xff0000 });
        this.waterVolumeArrow = new THREE.Mesh(arrowGeometry, arrowMaterial);
        this.waterVolumeArrow.rotation.z = Math.PI / 2;
        this.waterVolumeArrow.position.set(
            this.water.position.x + this.water.waterDimensions.width / 2 - 0.05,
            this.water.position.y + this.water.currentWaterHeight,
            this.water.position.z + 0.25
        );
        this.scene.add(this.waterVolumeArrow);
    }

    updateWaterVolumeIndicator = () => {
        if (!this.waterVolumeLabel || !this.waterVolumeArrow) return;

        // Use the effective volume from the Water class
        const waterVolume = this.water.getEffectiveVolume(); // In liters
        this.waterVolumeLabel.element.textContent = `${waterVolume.toFixed(2)} L`;

        const waterSurfaceY = this.water.position.y + this.water.currentWaterHeight;
        this.waterVolumeLabel.position.y = waterSurfaceY;
        this.waterVolumeArrow.position.y = waterSurfaceY;
    }

    handleWaterHeightChange = (event) => {
        let newHeight = parseFloat(event.target.value);
        newHeight = Math.min(newHeight, 0.4583); // Ensure the state doesn't exceed the max
        this.setState({ waterHeight: newHeight }, () => {
            this.water.increaseWaterHeight(newHeight);
        });
    };

    setupEnvMap = () => {
        const hdriPath = '/Resources/Practical2-FlameTest/kloppenheim_06_puresky_2k.hdr';
        new RGBELoader().load(hdriPath, (texture) => {
            texture.mapping = THREE.EquirectangularReflectionMapping;
            this.scene.background = texture;
            const pmremGenerator = new THREE.PMREMGenerator(this.renderer);
            this.scene.environment = pmremGenerator.fromEquirectangular(texture).texture;
            pmremGenerator.dispose();
        });
    }

    setupModel = () => {
        this.gltfLoader.load(
            'Resources/Practical3-ArchemedieseP/Swiming_pool.glb',
            (gltf) => {
                this.expModel = gltf.scene;
                this.expModel.userData.collide = true;
                this.expModel.traverse((child) => {
                    if (child.isMesh) {
                        child.material = new THREE.MeshStandardMaterial({
                            map: child.material.map || null,
                            metalness: 0.5,
                            roughness: 0.1,
                            envMap: this.scene.environment,
                            transparent: true,
                        });
                        child.castShadow = true;
                        child.receiveShadow = true;
                    }
                });

                this.expModel.position.set(0, 0, 0);
                this.expModel.scale.set(0.1, 0.1, 0.1);
                this.scene.add(this.expModel);

                if (this.expModel.userData.collide) {
                    const box = new THREE.Box3().setFromObject(this.expModel);
                    const helper = new THREE.Box3Helper(box, 0xffff00);
                    this.scene.add(helper);
                    this.collisionObjects.push({ model: this.expModel, boundingBox: box, helper });
                }

                this.setUpCubeGeometry();
                this.water.addObject(this.cube);
            }
        );

        this.gltfLoader.load(
            'Resources/Practical3-ArchemedieseP/Swiming_pool_right.glb',
            (gltf) => {
                this.expModel_right = gltf.scene;
                this.expModel_right.userData.collide = true;
                this.expModel_right.traverse((child) => {
                    if (child.isMesh) {
                        child.material = new THREE.MeshStandardMaterial({
                            map: child.material.map || null,
                            metalness: 0.5,
                            roughness: 0.1,
                            envMap: this.scene.environment,
                            transparent: true,
                        });
                        child.castShadow = true;
                        child.receiveShadow = true;
                    }
                });

                this.expModel_right.position.set(0, 0, 0);
                this.expModel_right.scale.set(0.1, 0.1, 0.1);
                this.scene.add(this.expModel_right);

                if (this.expModel_right.userData.collide) {
                    const box = new THREE.Box3().setFromObject(this.expModel_right);
                    const helper = new THREE.Box3Helper(box, 0x00ff00);
                    this.scene.add(helper);
                    this.collisionObjects.push({ model: this.expModel_right, boundingBox: box, helper });
                }
            }
        );

        this.gltfLoader.load(
            'Resources/Practical3-ArchemedieseP/Swiming_pool_left.glb',
            (gltf) => {
                this.expModel_left = gltf.scene;
                this.expModel_left.userData.collide = true;
                this.expModel_left.traverse((child) => {
                    if (child.isMesh) {
                        child.material = new THREE.MeshStandardMaterial({
                            map: child.material.map || null,
                            metalness: 0.5,
                            roughness: 0.1,
                            envMap: this.scene.environment,
                            transparent: true,
                        });
                        child.castShadow = true;
                        child.receiveShadow = true;
                    }
                });

                this.expModel_left.position.set(0, 0, 0);
                this.expModel_left.scale.set(0.1, 0.1, 0.1);
                this.scene.add(this.expModel_left);

                if (this.expModel_left.userData.collide) {
                    const box = new THREE.Box3().setFromObject(this.expModel_left);
                    const helper = new THREE.Box3Helper(box, 0x0000ff);
                    this.scene.add(helper);
                    this.collisionObjects.push({ model: this.expModel_left, boundingBox: box, helper });
                }
            }
        );

        this.gltfLoader.load(
            'Resources/Practical3-ArchemedieseP/Swiming_pool_back.glb',
            (gltf) => {
                this.expModel_left = gltf.scene;
                this.expModel_left.userData.collide = true;
                this.expModel_left.traverse((child) => {
                    if (child.isMesh) {
                        child.material = new THREE.MeshStandardMaterial({
                            map: child.material.map || null,
                            metalness: 0.5,
                            roughness: 0.1,
                            envMap: this.scene.environment,
                            transparent: true,
                        });
                        child.castShadow = true;
                        child.receiveShadow = true;
                    }
                });

                this.expModel_left.position.set(0, 0, 0);
                this.expModel_left.scale.set(0.1, 0.1, 0.1);
                this.scene.add(this.expModel_left);

                if (this.expModel_left.userData.collide) {
                    const box = new THREE.Box3().setFromObject(this.expModel_left);
                    const helper = new THREE.Box3Helper(box, 0x0000ff);
                    this.scene.add(helper);
                    this.collisionObjects.push({ model: this.expModel_left, boundingBox: box, helper });
                }
            }
        );
    }

    handleMaterialChange = (event) => {
        const material = event.target.value;
        const density = this.materialDensities[material];
        const { volume } = this.state;
        let mass = density * volume;

        const { min, max } = this.massRanges[material];
        mass = Math.max(min, Math.min(max, mass));

        this.setState({ selectedMaterial: material, mass }, () => {
            this.updateCubeProperties();
        });
    };

    handleMassChange = (event) => {
        let mass = parseFloat(event.target.value);
        const { selectedMaterial } = this.state;
        const { min, max } = this.massRanges[selectedMaterial];
        mass = Math.max(min, Math.min(max, mass));
        const density = this.materialDensities[selectedMaterial];
        const volume = mass / density;

        this.setState({ mass, volume }, () => {
            this.updateCubeProperties();
        });
    };

    handleVolumeChange = (event) => {
        const volume = parseFloat(event.target.value);
        const { selectedMaterial } = this.state;
        const density = this.materialDensities[selectedMaterial];
        let mass = density * volume;
        const { min, max } = this.massRanges[selectedMaterial];
        mass = Math.max(min, Math.min(max, mass));

        this.setState({ volume, mass }, () => {
            this.updateCubeProperties();
        });
    };

    updateCubeProperties = () => {
        const { mass, volume, selectedMaterial } = this.state;
        const size = Math.cbrt(volume);
        const appearance = this.materialAppearances[selectedMaterial];

        if (this.cube) {
            this.scene.remove(this.cube);
            this.scene.remove(this.cubeBoundingBoxHelper);
            this.cube.geometry.dispose();
            this.cube.material.dispose();
            this.water.removeObject(this.cube);
        }

        const geometry = new THREE.BoxGeometry(size, size, size);
        const material = new THREE.MeshStandardMaterial({
            map: new THREE.TextureLoader().load(appearance.map),

            color: appearance.color,
            metalness: appearance.metalness,
            roughness: appearance.roughness,
            envMap: this.scene.environment,
            envMapIntensity: 1.0,
        });
        this.cube = new THREE.Mesh(geometry, material);
        this.cube.userData = {
            collide: true,
            draggable: true,
            interactWithWater: true,
            mass: mass,
            volume: volume,
            velocity: new THREE.Vector3(),
            inWater: false,
            isBeingDragged: false,
        };

        if (this.collisionObjects.length > 0) {
            const poolTop = this.collisionObjects[0].boundingBox.max.y;
            const poolCenter = new THREE.Vector3();
            this.collisionObjects[0].boundingBox.getCenter(poolCenter);
            this.cube.position.set(poolCenter.x, poolTop + size / 2, poolCenter.z);
        } else {
            this.cube.position.set(0.1, 0.1, 0);
        }

        this.scene.add(this.cube);
        this.water.addObject(this.cube);

        const cubeBox = new THREE.Box3().setFromObject(this.cube);
        this.cubeBoundingBoxHelper = new THREE.Box3Helper(cubeBox, 0xff00ff);
        this.scene.add(this.cubeBoundingBoxHelper);
    };

    createDragPlane = () => {
        const planeGeometry = new THREE.PlaneGeometry(1000, 1000);
        const planeMaterial = new THREE.MeshBasicMaterial({ visible: false });
        this.dragPlane = new THREE.Mesh(planeGeometry, planeMaterial);
        this.scene.add(this.dragPlane);
    }

    setUpCubeGeometry = () => {
        const { mass, volume, selectedMaterial } = this.state;
        const size = Math.cbrt(volume);
        const appearance = this.materialAppearances[selectedMaterial];
        
        const geometry = new THREE.BoxGeometry(size, size, size);
        const material = new THREE.MeshStandardMaterial({
            map: new THREE.TextureLoader().load(appearance.map),
            color: appearance.color,
            metalness: appearance.metalness,
            roughness: appearance.roughness,
            envMap: this.scene.environment,
            envMapIntensity: 1.0,
        });
        this.cube = new THREE.Mesh(geometry, material);
        this.cube.userData = {
            collide: true,
            draggable: true,
            interactWithWater: true,
            mass: mass,
            volume: volume,
        };

        if (this.collisionObjects.length > 0) {
            const poolTop = this.collisionObjects[0].boundingBox.max.y;
            const poolCenter = new THREE.Vector3();
            this.collisionObjects[0].boundingBox.getCenter(poolCenter);
            
            this.cube.position.set(
                poolCenter.x,
                poolTop + size / 2,
                poolCenter.z
            );
        } else {
            this.cube.position.set(0.1, 0.1, 0);
        }
        
        this.scene.add(this.cube);

        const cubeBox = new THREE.Box3().setFromObject(this.cube);
        this.cubeBoundingBoxHelper = new THREE.Box3Helper(cubeBox, 0xff00ff);
        this.scene.add(this.cubeBoundingBoxHelper);
    }

    setupLight = () => {
        this.scene.add(new THREE.HemisphereLight(0xffffbb, 0x080820, 0.5));
        const directionalLight = new THREE.DirectionalLight(0xffffff, 2);
        directionalLight.position.set(5, 10, 5);
        directionalLight.castShadow = true;
        directionalLight.shadow.mapSize.width = 1024;
        directionalLight.shadow.mapSize.height = 1024;
        directionalLight.shadow.camera.near = 0.5;
        directionalLight.shadow.camera.far = 50;
        directionalLight.shadow.camera.left = -10;
        directionalLight.shadow.camera.right = 10;
        directionalLight.shadow.camera.top = 10;
        directionalLight.shadow.camera.bottom = -10;
        this.scene.add(directionalLight);
    }

    setupCamera = () => {
        this.camera = new THREE.PerspectiveCamera(60, (this.WNDSIZE.width / this.WNDSIZE.height), 0.1, 10000);
        this.camera.position.set(0.1417493988149943, 0.3982276689825735, 0.8850904346589407);
        this.camera.lookAt(this.scene.position);

        this.controls = new OrbitControls(this.camera, this.renderer.domElement);

        this.controls.target.set(0.1417493988149943, 0.24071066306680738, -0.03137214521460759)
        this.controls.enablePan = false;
        this.controls.enableZoom = false;
        // this.controls.target.set(0.15, 0, 0.01);
        this.controls.enableRotate = false;
        this.controls.minDistance = 0.1;
        this.controls.maxDistance = 500;
        this.controls.maxPolarAngle = Math.PI / 2;
        this.controls.update();
    }

    start = () => {
        if (!this.frameId) {
            this.frameId = requestAnimationFrame(this.update);
        }
    }

    stop = () => {
        cancelAnimationFrame(this.frameId);
    };

    resizeWindow = () => {
        this.WNDSIZE.width = this.mount.clientWidth;
        this.WNDSIZE.height = this.mount.clientHeight;
        this.camera.aspect = this.WNDSIZE.width / this.WNDSIZE.height;
        this.camera.updateProjectionMatrix();
        this.renderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
        this.labelRenderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
    }

    checkCollision = () => {
        if (!this.cube || this.collisionObjects.length === 0) return;

        const cubeBox = new THREE.Box3().setFromObject(this.cube);
        const cubeSize = new THREE.Vector3();
        cubeBox.getSize(cubeSize);

        let isColliding = false;

        this.collisionObjects.forEach((obj) => {
            if (!obj.model.userData.collide) return;

            obj.boundingBox.setFromObject(obj.model);

            if (cubeBox.intersectsBox(obj.boundingBox)) {
                isColliding = true;
                const objTop = obj.boundingBox.max.y;
                const cubeBottom = cubeBox.min.y;

                if (cubeBottom < objTop) {
                    this.cube.position.y = objTop + cubeSize.y / 2;
                    this.cube.userData.velocity = this.cube.userData.velocity || new THREE.Vector3();
                    this.cube.userData.velocity.y = 0;
                }

                const objCenter = new THREE.Vector3();
                obj.boundingBox.getCenter(objCenter);
                const objSize = new THREE.Vector3();
                obj.boundingBox.getSize(objSize);

                const halfWidth = objSize.x / 2;
                const halfDepth = objSize.z / 2;

                this.cube.position.x = Math.max(objCenter.x - halfWidth + cubeSize.x / 2,
                    Math.min(objCenter.x + halfWidth - cubeSize.x / 2, this.cube.position.x));
                this.cube.position.z = Math.max(objCenter.z - halfDepth + cubeSize.z / 2,
                    Math.min(objCenter.z + halfDepth - cubeSize.z / 2, this.cube.position.z));
            }
        });

        this.toggleBoundingBoxes(false);
    }

    toggleBoundingBoxes = (val) => {
        this.areBoundingBoxesVisible = val;
        this.collisionObjects.forEach((obj) => {
            obj.helper.visible = this.areBoundingBoxesVisible;
        });
        if (this.cubeBoundingBoxHelper) {
            this.cubeBoundingBoxHelper.visible = this.areBoundingBoxesVisible;
        }
    }

    handleKeyDown = (event) => {
        if (event.key === 'b' || event.key === 'B') {
            this.toggleBoundingBoxes();
        }
    }

    update = () => {
        const currentTime = performance.now();
        const deltaTime = (currentTime - this.lastTime) / 1000;
        this.lastTime = currentTime;

        this.controls.update();
        if (this.water) this.water.update(deltaTime);
        this.checkCollision();
        this.updateWaterVolumeIndicator();

        this.collisionObjects.forEach((obj) => {
            obj.boundingBox.setFromObject(obj.model);
            obj.helper.box = obj.boundingBox;
        });
        if (this.cubeBoundingBoxHelper) {
            const cubeBox = new THREE.Box3().setFromObject(this.cube);
            this.cubeBoundingBoxHelper.box = cubeBox;
        }

        this.renderer.render(this.scene, this.camera);
        this.labelRenderer.render(this.scene, this.camera);
        this.frameId = window.requestAnimationFrame(this.update);
    }

    render() {
        const { selectedMaterial, mass, volume, waterHeight } = this.state;
        const { min: massMin, max: massMax } = this.massRanges[selectedMaterial];
        const { min: volMin, max: volMax } = this.volumeRange;

        const volumeLiters = volume * 1000;

        const massTotalRange = 27 - 0.50;
        const massAllowedStart = ((massMin - 0.50) / massTotalRange) * 1;
        const massAllowedWidth = ((massMax - massMin) / massTotalRange) * 100;
        const massBackground = `linear-gradient(to right, 
            #444444 0%, 
            #444444 ${massAllowedStart}%, 
            #ffffff ${massAllowedStart}%, 
            #ffffff ${massAllowedStart + massAllowedWidth}%, 
            #444444 ${massAllowedStart + massAllowedWidth}%, 
            #444444 100%)`;

        const volumeBackground = 'linear-gradient(to right, #ffffff 0%, #ffffff 100%)';

        return (
            <div className="main">
                <div className="ExpTitle">Title: Archimedes' principle</div>
                
                <div style={{ position: 'absolute', top: 10, right: 10, zIndex: 1 }}>
                    <select
                        value={selectedMaterial}
                        onChange={this.handleMaterialChange}
                        style={{ marginBottom: 10, padding: 5 }}
                    >
                        <option value="wood">Wood</option>
                        <option value="pvc">PVC</option>
                        <option value="aluminum">Aluminum</option>
                    </select>
                    <div style={{ marginBottom: 10 }}>
                        <label>Mass (kg): {mass.toFixed(2)}</label>
                        <input
                            type="range"
                            min="0.50"
                            max="27"
                            step="0.01"
                            value={mass}
                            onChange={this.handleMassChange}
                            style={{
                                width: '100%',
                                background: massBackground,
                                appearance: 'none',
                                height: '8px',
                                borderRadius: '4px',
                                outline: 'none',
                            }}
                        />
                    </div>
                    <div style={{ marginBottom: 10 }}>
                        <label>Volume (L): {volumeLiters.toFixed(2)}</label>
                        <input
                            type="range"
                            min={volMin}
                            max={volMax}
                            step="0.0001"
                            value={volume}
                            onChange={this.handleVolumeChange}
                            style={{
                                width: '100%',
                                background: volumeBackground,
                                appearance: 'none',
                                height: '8px',
                                borderRadius: '4px',
                                outline: 'none',
                            }}
                        />
                    </div>
                    <div>
                        <label>Water Height (m): {waterHeight.toFixed(4)}</label>
                        <input
                            type="range"
                            min="0.208333" // Initial height for 100L
                            max="0.4583" // Restricted maximum height
                            step="0.01"
                            value={waterHeight}
                            onChange={this.handleWaterHeightChange}
                            style={{
                                width: '100%',
                                background: volumeBackground,
                                appearance: 'none',
                                height: '8px',
                                borderRadius: '4px',
                                outline: 'none',
                            }}
                        />
                    </div>
                </div>
                <div className="canvasContainer">
                    <div ref={(ref) => (this.mount = ref)} className="canvasContainer__maincanvas" />
                </div>

                <style>{`
                    input[type="range"]::-webkit-slider-thumb {
                        appearance: none;
                        width: 16px;
                        height: 16px;
                        background: #4CAF50;
                        border-radius: 50%;
                        cursor: pointer;
                    }
                    input[type="range"]::-moz-range-thumb {
                        width: 16px;
                        height: 16px;
                        background: #4CAF50;
                        border-radius: 50%,
                        cursor: pointer;
                    }
                `}</style>
            </div>
        );
    }
}

export default ArchemedisePrincipalComponent;