import React, { Component } from 'react';
import * as THREE from "three";

import { TrackballControls } from 'three/examples/jsm/controls/TrackballControls.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { CSS2DRenderer, CSS2DObject } from 'three/examples/jsm/renderers/CSS2DRenderer.js';
import { getBunsenFlame } from './getBunsenFlame.js';
import ChemistryLabStepper from './CustomStepper/index.js';


class MetalOxideComponent extends Component {
    
    constructor(props) {
        super(props);
        this.scene = null;
        this.camera = null;
        this.renderer = null;
        this.cube = null;
        this.light = null;
        this.frameId = null;
        this.controls = null;

        this.WNDSIZE = { width:0, height: 0};

        this.gltfLoader = null;

        this.isDragging = false;
        this.dragObject = null;
        this.dragPlane = null;
        this.initialDragOffset = new THREE.Vector3();

        this.raycaster = null;
        this.pointer = null;
        this.moveMouse = new THREE.Vector2();
        this.lastTime = performance.now();
        this.rotateAsh = false;
        this.rotateBottle = false;
        this.waterMesh = null;
        this.labelRenderer = null;

        this.magnesiumMesh = null;
        this.sulpherMesh = null;

        this.bunsenFlame = null;
        this.gas = null;
        this.gasDisplay = false;
        this.flaskDisplay = false;
        this.flaskGasDisplay = false;

        this.expModel = null;
        this.flaskGasMesh = null;
        this.flaskGasFilledMesh = null;
        this.sulpherSpoonMesh = null;
        this.sulpherBurn = true;

        this.rotateNBottle = false;

        this.fireSourse = null;
        this.smokeEffect = null;
        this.clock = null;
        this.labels = {}; // Store labels for all meshes

        this.state = {
            magnesiumStep : true,
            sulpherStep : false,
            applyGlass : false,
            ashDisplay : false,
        }
    }
    
    componentDidMount() {
        this.initialize();
    }

    componentWillUnmount() {
        this.stop();
        window.removeEventListener('keydown', this.handleKeyDown);
        window.removeEventListener('mousedown', this.onPointerClick);
        window.removeEventListener('mouseup', this.onPointerUp);
        window.removeEventListener('mousemove', this.onMouseMove);
        window.removeEventListener("resize", this.resizeWindow);
    }

    onPointerUp = () => {
        this.isDragging = false;
        document.body.style.cursor = "default";
        if(this.dragObject){
            console.log("-----",this.dragObject.name);
            console.log("Position-----",this.dragObject.position);
        }
        if(this.dragObject){
            if(this.state.magnesiumStep)
            {
                if(this.dragObject.name === "magnesium") {          
                    this.dragObject.position.set(0.9265374671274257, 1.6986295436924048, -0.1357724368572235);
                    this.dragObject.rotation.set(0, 0, 0);
                    this.magnesiumMesh = this.dragObject;
                    setTimeout(()=>{
                        this.setState({ashDisplay :true})
                    },2000);
                }
                if(this.dragObject.name === "Ash") {
                    this.dragObject.position.set(1.695426096008462, 1.3813628924852204, -0.08505894988775253);
                    this.rotateAsh = true;
                }
                if(this.dragObject.name === "phenolpthelin") {
                    this.dragObject.position.set(2.1177139149443223, 1.4072669637219815,-0.06561276316642761);
                    this.rotateBottle = true;
                }
            }

            if(this.state.sulpherStep) {
                if(this.dragObject.name === "sulpher"){
                    //code
                    this.dragObject.position.set(0.8220306879923285, 1.9398560024335616, -0.14613483846187592);
                    // this.gas.updatePosition(new THREE.Vector3(this.dragObject.position.x, 0.0, 0.0));
                    this.gasDisplay = true;
                    this.sulpherMesh = this.dragObject;
                }

                if(this.dragObject.name === "phenolpthelin_sulpher") {
                    this.dragObject.position.set(1.6325894122805193, 1.4263026499750548,-0.06561276316642761);
                    this.rotateNBottle = true;
                }
            }
        }
        if (this.dragObject) {
            this.dragObject.userData.isBeingDragged = false;
            this.dragObject = null;
        }
    };

    onPointerClick = (event) => {
        this.pointer.x = (event.clientX / window.innerWidth) * 2 - 1;
        this.pointer.y = -(event.clientY / window.innerHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.pointer, this.camera);

        const draggableObjects = [];
        this.scene.traverse((child) => {
            if (child.userData.draggable) {
                draggableObjects.push(child);
            }
        });

        const intersects = this.raycaster.intersectObjects(draggableObjects, true);

        if (intersects.length > 0) {
            this.dragObject = intersects[0].object;
            this.dragObject.userData.isBeingDragged = true;
            this.isDragging = true;

            const worldPos = this.dragObject.position.clone();
            if (this.dragObject.parent) {
                this.dragObject.parent.localToWorld(worldPos);
            }
            this.dragPlane.position.copy(worldPos);
            this.dragPlane.lookAt(this.camera.position);

            const planeIntersects = this.raycaster.intersectObject(this.dragPlane);
            if (planeIntersects.length > 0) {
                this.initialDragOffset.subVectors(worldPos, planeIntersects[0].point);
            } else {
                this.initialDragOffset.set(0, 0, 0);
            }
        }
    }

    onMouseMove = (event) => {
        this.moveMouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        this.moveMouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.moveMouse, this.camera);

        const draggableObjects = [];
        this.scene.traverse((child) => {
            if (child.userData.draggable) {
                draggableObjects.push(child);
            }
        });
        const intersects = this.raycaster.intersectObjects(draggableObjects, true);

        document.body.style.cursor = intersects.length > 0 ? "pointer" : "default";

        if (this.isDragging && this.dragObject) {
            const intersects = this.raycaster.intersectObject(this.dragPlane);
            if (intersects.length > 0) {
                const intersection = intersects[0].point;
                const newWorldPos = intersection.add(this.initialDragOffset);

                let localPos = newWorldPos.clone();
                if (this.dragObject.parent) {
                    this.dragObject.parent.worldToLocal(localPos);
                }
                this.dragObject.position.x = localPos.x;
                this.dragObject.position.y = localPos.y;

                // Update label position
                const label = this.labels[this.dragObject.name.toLowerCase()];
                if (label) {
                    label.position.copy(this.dragObject.position).add(new THREE.Vector3(0, 0.1, 0));
                }

                if(this.dragObject.name === "sulpher"){
                    //code
                    // this.gas.updatePosition(this.dragObject.position);
                    // this.sulpherMesh = this.dragObject;
                }
            }
        }
    }

    setupEnvMap = () => {
        const hdriPath = '/Resources/Practical2-FlameTest/back.hdr';
        new RGBELoader().load(hdriPath, (texture) => {
            texture.mapping = THREE.EquirectangularReflectionMapping;
            this.scene.background = texture;
            const pmremGenerator = new THREE.PMREMGenerator(this.renderer);
            this.scene.environment = pmremGenerator.fromEquirectangular(texture).texture;
            pmremGenerator.dispose();
        });
    }

    initialize = () => {
        this.scene = new THREE.Scene();

        this.WNDSIZE.width = this.mount.clientWidth;
        this.WNDSIZE.height = this.mount.clientHeight;

        this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
        this.renderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
        this.renderer.toneMapping = THREE.ACESFilmicToneMapping;
        this.renderer.toneMappingExposure = 0.4;
        this.renderer.shadowMap.enabled = true;
        this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
        this.renderer.outputEncoding = THREE.sRGBEncoding;
      
        this.mount.appendChild(this.renderer.domElement);

        this.raycaster = new THREE.Raycaster();
        this.pointer = new THREE.Vector2();
        this.gltfLoader = new GLTFLoader();

        this.clock = new THREE.Clock();

        this.light = new THREE.HemisphereLight(0xffffbb, 0x080820, 1);
        this.scene.add(this.light);

        this.labelRenderer = new CSS2DRenderer();
        this.labelRenderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
        this.labelRenderer.domElement.style.position = 'absolute';
        this.labelRenderer.domElement.style.top = '0px';
        this.labelRenderer.domElement.style.pointerEvents = 'none';
        this.mount.appendChild(this.labelRenderer.domElement);

        this.setupEnvMap();
        this.setupModel();
        this.initFire();
        this.setupCamera();
        this.renderScene();

        
        this.createDragPlane();
        this.setupLight();

        this.start();

        window.addEventListener('mousedown', this.onPointerClick);
        window.addEventListener('mouseup', this.onPointerUp);
        window.addEventListener('mousemove', this.onMouseMove);
        window.addEventListener("resize", this.resizeWindow);
        window.addEventListener('keydown', this.handleKeyDown);
    }

    setupIndicator = () => {
        const createLabel = (text, position) => {
            const labelDiv = document.createElement('div');
            labelDiv.className = 'label';
            labelDiv.style.backgroundColor = 'rgba(0, 0, 0, 0.8)';
            labelDiv.style.padding = '5px';
            labelDiv.style.borderRadius = '5px';
            labelDiv.style.fontSize = '14px';
            labelDiv.style.fontFamily = 'Arial, sans-serif';
            labelDiv.style.color = 'white';
            labelDiv.textContent = text;
            const label = new CSS2DObject(labelDiv);
            label.position.copy(position);
            this.scene.add(label);
            return label;
        };

        if (this.expModel) {
            this.expModel.traverse((child) => {
                if (child.isMesh) {
                    let labelText = '';
                    let offsetY = 0.1;

                    if (child.name === "magnesium") {
                        labelText = "Magnesium";
                        this.labels.magnesium = createLabel(labelText, child.position.clone().add(new THREE.Vector3(0, offsetY, 0)));
                    } else if (child.name === "phenolpthelin") {
                        labelText = "Phenolphthalein";
                        this.labels.phenolpthelin = createLabel(labelText, child.position.clone().add(new THREE.Vector3(0, offsetY, 0)));
                    } else if (child.name === "Ash") {
                        labelText = "Ash";
                        this.labels.ash = createLabel(labelText, child.position.clone().add(new THREE.Vector3(0, offsetY, 0)));
                        this.labels.ash.visible = false;
                    } else if (child.name === "water_beaker2") {
                        labelText = "Water";
                        this.labels.water = createLabel(labelText, child.position.clone().add(new THREE.Vector3(0, offsetY, 0)));
                        this.labels.water.visible = false;
                    }
                }
            });
        }
    }

    initFire = () => {
        let { renderer, scene, camera, fireSourse } = this;
        let position;
        if (fireSourse) {
            position = fireSourse.position.clone();
        } else {
            position = new THREE.Vector3(0.0, 0.0,0.0);
        }

        this.bunsenFlame = getBunsenFlame({
            camera: this.camera,
            emitter: { position: new THREE.Vector3(0.084, 0.163, -0.005) },
            parent: this.scene,
            rate: 80,
            texture: "./Resources/fire.png",
            colors: new THREE.Vector4(0.8, 0.8, 0.8, 0.6),
            initialScale: { x: 0.15, y: 0.02, z: 0.02 },
            objectsToCollide: this.objectsToCollide
        });

        this.bunsenFlame.points.material.depthTest = false;
        this.bunsenFlame.points.material.depthWrite = true;

         //Gas
 
            this.gas = getBunsenFlame({
                camera: this.camera,
                emitter: { position: new THREE.Vector3(0.084, 0.193, -0.005) },
                parent: this.scene,
                rate: 20,
                texture: "./Resources/fire.png",
                colors: new THREE.Vector4(1.5, 1.8, 0.5, 0.2),
                initialScale: { x: 0.15, y: 0.02, z: 0.02 },
                objectsToCollide: this.objectsToCollide
            });

            this.gas.points.material.depthTest = false;
            this.gas.points.material.depthWrite = true;

     
   
    }


    setupModel = () => {
        this.gltfLoader.load(
            'Resources/Practical4-Oxidise/chemExp.glb',
            (gltf) => {
                this.expModel = gltf.scene;

                this.expModel.traverse((child) => {
                    if (child.isMesh) {
                        child.material = new THREE.MeshStandardMaterial({
                            map: child.material.map || null,
                            metalness: 0.5,
                            roughness: 0.1,
                            envMap: this.scene.environment,
                            side: THREE.DoubleSide,
                        });
                        child.castShadow = true;
                        child.receiveShadow = true;
                        if(child.name == "beaker_2") {
                            child.material.transparent = true;
                            child.material.opacity = 0.3;
                        }
                        if (child.isMesh && child.name === "Ash") {
                            child.visible = false;
                        }
                        if(child.name ==="water_beaker2") {
                            child.visible = false;
                        }
                        if(child.name == "sulpher") {
                            child.visible = false;

                            this.sulpherMesh = child;
                            child.userData.draggable = true;
                        }
                        if(child.name == "magnesium") {
                            child.userData.draggable = true;
                        }

                        if (child.isMesh && child.name === "falsk_Gas") {
                            this.flaskGasMesh = child;
                         
                            child.visible = false;
                        }
                        if (child.isMesh && child.name === "falsk_GasFilled") {
                            this.flaskGasFilledMesh = child;
                            child.material = new THREE.MeshStandardMaterial({
                                map: child.material.map || null,
                                metalness: 0.5,
                                roughness: 0.1,
                                envMap: this.scene.environment,
                                side: THREE.DoubleSide,
                            });
                            child.visible = false;
                        }


                    }
                });

                this.expModel.position.set(0, 0, 0);
                this.expModel.scale.set(0.1, 0.1, 0.1);
                this.scene.add(this.expModel);
                this.setupIndicator(); // Initialize labels after model is added
            }
        );
    }

    setupLight = () => {
        const ambientLight = new THREE.AmbientLight(0xffffff, 0.2);
        this.scene.add(ambientLight);

        const directionalLight = new THREE.DirectionalLight(0xffffff, 0.6);
        directionalLight.position.set(0.5, 1.0, 0.5);
        directionalLight.castShadow = false;
        this.scene.add(directionalLight);

        const pointLight = new THREE.PointLight(0xffffff, 0.8, 0.7);
        pointLight.position.set(0.1, 0.2, 0.05);
        this.scene.add(pointLight);

        const pointLight2 = new THREE.PointLight(0xffffff, 0.5, 0.7);
        pointLight2.position.set(0.25, 0.2, 0.03);
        this.scene.add(pointLight2);
    }

    setupCamera = () => {
        this.camera = new THREE.PerspectiveCamera(60, (this.WNDSIZE.width / this.WNDSIZE.height), 0.1, 10000);
        this.camera.position.set(0.142312588411287,0.16209877756769583, 0.17036742976745373);
        this.camera.lookAt(this.scene.position);

        this.controls = new OrbitControls(this.camera, this.renderer.domElement);
        this.controls.target.set(0.13593197914760535, 0.16209877756769583, -0.014584321974646685);
        
        this.controls.enablePan = false;
        this.controls.enableZoom = false;
        this.controls.enableRotate = false;

        this.controls.minDistance = 0.01;
        this.controls.maxDistance = 500;
        this.controls.maxPolarAngle = Math.PI / 2;
        this.controls.update();
    }

    start = () => {
        if(!this.frameId) {
            this.frameId = requestAnimationFrame(this.update);
        }
    }

    stop = () => {
        cancelAnimationFrame(this.frameId);
    };

    resizeWindow = () => {
        this.WNDSIZE.width = this.mount.clientWidth;
        this.WNDSIZE.height = this.mount.clientHeight;

        this.camera.aspect = this.WNDSIZE.width / this.WNDSIZE.height;
        this.camera.updateProjectionMatrix();

        this.renderer.setSize( this.WNDSIZE.width, this.WNDSIZE.height );
    }

    createDragPlane = () => {
        const planeGeometry = new THREE.PlaneGeometry(1000, 1000);
        const planeMaterial = new THREE.MeshBasicMaterial({ visible: false });
        this.dragPlane = new THREE.Mesh(planeGeometry, planeMaterial);
        this.scene.add(this.dragPlane);
    }

    handleSteps = () => {
        if(this.state.magnesiumStep){
            if(this.expModel) {
                this.expModel.traverse((child) => {
                    if (child.isMesh) {
                        if ( child.name === "phenolpthelin_sulpher") {
                            child.visible = false
                        }

                    }
                });
                }

            if(this.state.ashDisplay) {
                this.expModel.traverse((child) => {
                    if (child.isMesh) {
                        if (child.isMesh && child.name === "magnesium" || child.name === "dish" || child.name === "Ash" || child.name === "phenolpthelin_sulpher") {
                            child.visible = false
                        }

                        if (child.name === "magnesium" || child.name === "dish") {
                            child.visible = false;
                            if (child.name === "magnesium" && this.labels.magnesium) {
                                this.labels.magnesium.visible = false;
                            }
                        }
                        if (child.name === "Ash") {
                            child.visible = true;
                            child.userData.draggable = true;
                            if (this.labels.ash) this.labels.ash.visible = true;
                        }
                    }
                });

                if(this.rotateAsh) {
                    this.expModel.traverse((child) => {
                        if (child.isMesh && (child.name === "Ash" || child.name === "water_beaker2")) {
                            if (child.rotation.z >= -0.8) {
                                child.rotation.z -= 0.01;
                                console.log("rotation",child.rotation.z);
                            }
                            if (child.rotation.z <= -0.8) {
                                child.visible = false;
                                if (child.name === "water_beaker2") {
                                    child.visible = true;
                                    this.waterMesh = child;
                                    if (this.labels.water) this.labels.water.visible = true;
                                }
                            }
                        }
                    });
                }

                if(this.expModel.getObjectByName("water_beaker2").visible) {

                    this.expModel.traverse((child) => {
                        if (child.isMesh && child.name === "phenolpthelin") {
                            child.userData.draggable = true;
                            if (this.labels.phenolpthelin) this.labels.phenolpthelin.visible = true;
                        }
                    });

                    if(this.rotateBottle) {
                        this.expModel.traverse((child) => {
                            if (child.isMesh && child.name === "phenolpthelin") {
                                if (child.rotation.z <= 0.8) {
                                    child.rotation.z += 0.01;
                                }
                                if (child.rotation.z >= 0.8) {
                                    child.visible = false;
                                    this.waterMesh.material.color.set(0xFF00FF); // Change color to pink
                                    if (this.labels.phenolpthelin) this.labels.phenolpthelin.visible = false;
                                    if (this.labels.water) {
                                        this.labels.water.element.textContent = "Oxide"; // Update label to "Oxide"
                                    }
                                }
                            }
                        });
                    }
                }
            }
        }

        if(this.state.sulpherStep) {
            if (this.expModel) {
                this.expModel.traverse((child) => {
                    if (child.isMesh && (child.name === "magnesium" || child.name === "dish" || child.name === "Ash" || child.name === "water_beaker2" || child.name === "beaker_2" || child.name === "phenolpthelin")) {
                        child.visible = false;
                    }
                });
            }
    
            if (this.sulpherMesh) {
                if (this.sulpherBurn) {
                    this.sulpherMesh.visible = true;
                    if (this.flaskDisplay) {
                        this.flaskGasMesh.visible = true;
                        this.flaskGasMesh.material = new THREE.MeshStandardMaterial({
                            map: this.flaskGasMesh.material.map || null,
                            metalness: 0.5,
                            roughness: 0.1,
                            envMap: this.scene.environment,
                            side: THREE.DoubleSide,
                            transparent: true,
                            opacity: 0.3,
                        });
                    }
    
                    setTimeout(() => {
                        this.gas.update(0.0016, false);
                        this.flaskGasMesh.visible = false;
                        this.flaskDisplay = false;
                        this.sulpherBurn = false;
                        this.flaskGasMesh.visible = false;
                        this.gasDisplay = false;
                        this.sulpherMesh.visible = false;
                        this.flaskGasDisplay = true;
                    }, 8000);
                }
    
                if (this.flaskGasDisplay) {
                    this.expModel.traverse((child) => {
                        if (child.isMesh && child.name === "falsk_GasFilled") {
                            child.visible = true;
                            child.material = new THREE.MeshStandardMaterial({
                                map: this.flaskGasMesh.material.map || null,
                                metalness: 0.5,
                                roughness: 0.1,
                                envMap: this.scene.environment,
                                side: THREE.DoubleSide,
                                transparent: true,
                                opacity: 0.3,
                            });
                        }
    
                        if (child.isMesh && child.name === "phenolpthelin_sulpher") {
                            child.userData.draggable = true;
                            if (this.labels.phenolpthelin) this.labels.phenolpthelin.visible = true;
                        }
                    });
    
                    if (this.rotateNBottle) {
                        this.expModel.traverse((child) => {
                            if (child.isMesh && child.name === "phenolpthelin_sulpher") {
                                child.userData.draggable = true;
                                if (child.rotation.z <= 0.9) {
                                    child.rotation.z += 0.01;
                                }
                                if (child.rotation.z >= 0.9) {
                                    child.visible = false;
                                    if (this.labels.phenolpthelin) this.labels.phenolpthelin.visible = false;
    
                                    // Function to create a label
                                    const createLabel = (text, position) => {
                                        const labelDiv = document.createElement('div');
                                        labelDiv.className = 'label';
                                        labelDiv.style.backgroundColor = 'rgba(0, 0, 0, 0.8)';
                                        labelDiv.style.padding = '5px';
                                        labelDiv.style.borderRadius = '5px';
                                        labelDiv.style.fontSize = '14px';
                                        labelDiv.style.fontFamily = 'Arial, sans-serif';
                                        labelDiv.style.color = 'white';
                                        labelDiv.textContent = text;
                                        const label = new CSS2DObject(labelDiv);
                                        label.position.copy(position);
                                        this.scene.add(label);
                                        return label;
                                    };
    
                                    // Calculate screen center and bottom positions in 3D space
                                    const vector = new THREE.Vector3();
                                    const widthHalf = this.WNDSIZE.width / 2;
                                    const heightHalf = this.WNDSIZE.height / 2;
    
                                
                                    // Bottom of the screen
                                    vector.set(0, -0.7, -0.5); // Adjust y to near bottom, z slightly in front
                                    vector.unproject(this.camera); // Convert from NDC to world coordinates
                                    if (!this.labels.noChangeBottom) {
                                        this.labels.noChangeBottom = createLabel("No change in color", vector);
                                    }
                                }
                            }
                        });
                    }
                }
            }
          
        }
    }

    update = () => {
        const deltaTime = this.clock.getDelta();
        this.bunsenFlame.update(deltaTime, true);

        // this.gas.update(deltaTime, true);
        if(this.gas && this.sulpherMesh && this.gasDisplay){
            
            setTimeout(() =>{
                this.gas.update(0.0016, true);
                this.flaskDisplay = true;
            },2000)
        }

        

        this.handleSteps();

        this.controls.update();
        this.renderScene();
        this.frameId = window.requestAnimationFrame(this.update);
    }

    renderScene = () => {
        let { renderer, scene, camera } = this;
        if(this.labelRenderer) {
            this.labelRenderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
            this.labelRenderer.render(scene, camera);
        }
        if(renderer) {
            renderer.render(scene, camera);
        }
    }

    render() {
        return (
            <div className="main">
                <div className="ExpTitle">Title: Preparation of Oxides, Hydroxides and Oxyacids of Magnesium and Sulphur</div>

                
                <button className='btn_state1' onClick={
                    (event) => {
                        this.setState({
                            magnesiumStep : true,
                            sulpherStep : false,
                        });
                    }
                }> Magnesium </button>
                <button className='btn_state2' onClick={
                    (event) => {
                        this.setState({
                            magnesiumStep : false,
                            sulpherStep : true,
                        });
                    }

                }> Sulphur </button> 

                <div className="canvasContainer">
                    <div ref={ref => (this.mount = ref)} className="canvasContainer__maincanvas"/>
                </div>
                <div className="stepper">
                    {/* <Stepper currentStep={currentStep} onStepChange={this.handleStepChange} /> */}
                    <ChemistryLabStepper activeState = {this.sublimation} type = "exp4" process = {"magnesium"}/>
                </div>
            </div>
        )
    }
}

export default MetalOxideComponent;