import React, { Component } from 'react';
import * as THREE from "three";
import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { getBunsenFlame } from './getBunsenFlame.js';

const Precipitate_Info = {
  Combination_Reaction: {
    title: "Combination Reactions (Synthesis)",
    description: "Two or more reactants combine to form a single product. A + B → AB",
    example: "Example: 2Mg + O₂ → 2MgO (Magnesium + Oxygen → Magnesium Oxide)",
    characteristics: "• Typically exothermic\n• Forms a single product\n• Common in element-compound reactions",
    steps: "• Drag the magnesium ribbon on burner \n• This forms a ash into the flask.\n• Drag the napthelene bottle towards the flask.\n• This forms a pink solution into the flask."

  },
  Decomposition_Reactions: {
    title: "Decomposition Reactions",
    description: "A single compound breaks down into two or more elements or simpler compounds. AB → A + B",
    example: "Example: 2H₂O → 2H₂ + O₂ (Water → Hydrogen + Oxygen)",
    characteristics: "• Often requires energy input (heat, light, electricity)\n• Opposite of combination reaction\n• Produces multiple products",
    steps: "• Drag the beaker to burner which contains 1g of Potassium Chlorate\n• This forms bubbles, drag the toothpick towards the mouth of beaker - it burst into flame ."

  },
  Displacement_Reactions: {
    title: "Displacement Reactions (Single Replacement)",
    description: "An element replaces another element in a compound. A + BC → AC + B",
    example: "Example: Zn + CuSO₄ → ZnSO₄ + Cu (Zinc + Copper Sulfate → Zinc Sulfate + Copper)",
    characteristics: "• Involves a more reactive element\n• Follows activity series\n• One element is displaced",
    steps: "• Drag the beaker on burner which contains the mixture of 3ml HCL and Zn\n• Yellow gas is displaced and collected into inverted falsk."

  },
  Double_Displacement_Reactions: {
    title: "Double Displacement Reactions",
    description: "Ions from two compounds exchange places to form two new compounds.",
    example: "Example: KI + Pb(NO₃)₂ → PbI₂ + KNO₃ (yellow precipitate forms)",
    characteristics: "• Often produces a precipitate\n• Involves ion exchange\n• Common in aqueous solutions",
    steps: "• Add 1 mL KI from left beaker into left beaker by dragging right side beaker \n• Instant yellow precipitate appears after adding 1 ml of Kl into 1 mL Pb(NO₃)₂."

  }
};

const BasemodelPath = 'Resources/Practical6-combinations/chemExpBase.glb';
const EXPERIMENTS = {
  Combination_Reaction: {
    modelPath: 'Resources/Practical6-combinations/chemExp.glb',
    scale: 0.1,
    position: new THREE.Vector3(0, 0, 0)
  },
  Decomposition_Reactions: {
    modelPath: 'Resources/Practical6-combinations/chemExp_2.glb',
    scale: 0.1,
    position: new THREE.Vector3(0, 0, 0)
  },
  Displacement_Reactions: {
    modelPath: 'Resources/Practical6-combinations/chemExp_3.glb',
    scale: 0.1,
    position: new THREE.Vector3(0, 0, 0)
  },
  Double_Displacement_Reactions: {
    modelPath: 'Resources/Practical7-doubleDisp/chemExp.glb',
    scale: 0.1,
    position: new THREE.Vector3(0, 0, 0)
  }
};

class ReactionsExperiment extends Component {
  constructor(props) {
    super(props);
    this.scene = null;
    this.camera = null;
    this.renderer = null;
    this.light = null;
    this.frameId = null;
    this.controls = null;

    this.isDragging = false;
    this.dragObject = null;
    this.dragPlane = null;
    this.initialDragOffset = new THREE.Vector3();

    this.raycaster = null;
    this.pointer = null;
    this.moveMouse = new THREE.Vector2();

    this.currentModel = null;
    this.baseModel = null;
    this.models = {};
    this.displayGas = false;
    this.toothPlace = false;
    this.beakerOnPlace = false;
    this.beakerOnPlace2 = false;

    this.rotatePhenol = false;
    this.toothPick = null;
    this.gas = null;
    this.fire2 = null;
    this.WNDSIZE = { width: 0, height: 0 };

    this.state = {
      applyGlass: false,
      combintaionState: false,
      combination_step1: false,
      selectedPrecipitate: "Combination_Reaction"
    };
  }

  componentDidMount() {
    this.initialize();
  }

  componentWillUnmount() {
    this.stop();
    window.removeEventListener('mousedown', this.onPointerClick);
    window.removeEventListener('mouseup', this.onPointerUp);
    window.removeEventListener('mousemove', this.onMouseMove);
    window.removeEventListener("resize", this.resizeWindow);
  }

  initialize = () => {
    this.scene = new THREE.Scene();
    this.WNDSIZE.width = this.mount.clientWidth;
    this.WNDSIZE.height = this.mount.clientHeight;

    this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
    this.renderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
    this.renderer.toneMapping = THREE.ACESFilmicToneMapping;
    this.renderer.toneMappingExposure = 0.5;
    this.renderer.shadowMap.enabled = true;
    this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
    this.renderer.outputEncoding = THREE.sRGBEncoding;

    this.mount.appendChild(this.renderer.domElement);
    this.renderer.setPixelRatio(window.devicePixelRatio);

    this.raycaster = new THREE.Raycaster();
    this.pointer = new THREE.Vector2();
    this.gltfLoader = new GLTFLoader();
    this.clock = new THREE.Clock();

    this.light = new THREE.HemisphereLight(0xffffbb, 0x080820, 1);
    this.scene.add(this.light);

    this.setupEnvMap();
    this.setupLight();
    this.createDragPlane();
    this.initFire();
    this.setupCamera();
    this.loadAllModels();
    this.renderScene();

    this.start();

    window.addEventListener('mousedown', this.onPointerClick);
    window.addEventListener('mouseup', this.onPointerUp);
    window.addEventListener('mousemove', this.onMouseMove);
    window.addEventListener("resize", this.resizeWindow);
  }

  loadAllModels = () => {
    this.gltfLoader.load(
      BasemodelPath,
      (gltf) => {
        this.baseModel = gltf.scene;
        this.baseModel.scale.set(0.1, 0.1, 0.1);
        this.baseModel.position.set(0, 0, 0);
        this.baseModel.traverse((child) => {
          if (child.isMesh) {
            child.material = new THREE.MeshStandardMaterial({
              map: child.material.map || null,
              metalness: 0.5,
              roughness: 0.1,
              envMap: this.scene.environment,
              side: THREE.DoubleSide,
              transparent: true
            });
          }
        });
        this.scene.add(this.baseModel);
      },
      undefined,
      (error) => {
        console.error('Failed to load base model:', error);
      }
    );

    Object.keys(EXPERIMENTS).forEach(exp => {
      this.loadModel(exp);
    });
  }

  loadModel = (experiment) => {
    const config = EXPERIMENTS[experiment];
    this.gltfLoader.load(
      config.modelPath,
      (gltf) => {
        const model = gltf.scene;
        model.userData.collide = true;
        model.scale.set(config.scale, config.scale, config.scale);
        model.position.copy(config.position);

        const initialPositions = {};
        const initialVisibilities = {};
        const initialDraggables = {};
        model.traverse((child) => {
          if (child.isMesh) {
            initialPositions[child.name] = child.position.clone();
            initialVisibilities[child.name] = child.visible;
            initialDraggables[child.name] = child.userData.draggable || false;
            child.material = new THREE.MeshStandardMaterial({
              map: child.material.map || null,
              metalness: 0.5,
              roughness: 0.1,
              envMap: this.scene.environment,
              side: THREE.DoubleSide,
              transparent: true
            });

            if (experiment === "Combination_Reaction") {
              if (child.name === "magnesium") {
                child.userData.draggable = true;
                initialDraggables[child.name] = true;
              }
              if (child.name === "falsk_GasFilled" || child.name === "falsk_Gas") {
                child.material.opacity = 0.1;
                child.visible = false;
                child.userData.draggable = false;
                child.renderOrder = 2;
                initialVisibilities[child.name] = false;
                initialDraggables[child.name] = false;
              }
              if (child.name === "pink") {
                child.material = new THREE.MeshStandardMaterial({
                  color: 0xff69b4,
                  metalness: 0.5,
                  roughness: 0.5,
                  envMap: this.scene.environment,
                  transparent: false
                });
                child.visible = false;
                child.renderOrder = 1;
                initialVisibilities[child.name] = false;
                initialDraggables[child.name] = false;
              }
              if (child.name === "phenolpthelin_sulpher") {
                child.userData.draggable = false;
                initialDraggables[child.name] = false;
              }
            } else if (experiment === "Decomposition_Reactions") {
              if (child.name === "falsk_Gas_exp6_1") {
                child.userData.draggable = true;
                initialDraggables[child.name] = true;
              }
              if (child.name === "ToothPick") {
                child.userData.draggable = false;
                initialDraggables[child.name] = false;
              }
            } else if (experiment === "Displacement_Reactions") {
              if (child.name === "lab_beaker_b_0002") {
                child.userData.draggable = true;
                initialDraggables[child.name] = true;
              }
              if (child.name === "falsk_Gas" || child.name === "falsk_Gas_exp6_3") {
                child.material = new THREE.MeshStandardMaterial({
                  map: child.material.map || null,
                  metalness: 0.5,
                  roughness: 0.1,
                  envMap: this.scene.environment,
                  side: THREE.DoubleSide,
                  transparent: true,
                  opacity: 0.4
                });
                child.visible = false;
                child.userData.draggable = false;
                initialVisibilities[child.name] = false;
                initialDraggables[child.name] = false;
              }
            } else if (experiment === "Double_Displacement_Reactions") {
              if (child.name === "lab_beaker_b_0002") {
                child.userData.draggable = true;
                initialDraggables[child.name] = true;
              }
              if (child.name === "yellow") {
                child.visible = false;
              }
              if (child.name === "falsk_Gas" || child.name === "falsk_Gas_exp6_3") {
                child.material = new THREE.MeshStandardMaterial({
                  map: child.material.map || null,
                  metalness: 0.5,
                  roughness: 0.1,
                  envMap: this.scene.environment,
                  side: THREE.DoubleSide,
                  transparent: true,
                  opacity: 0.4
                });
                child.visible = false;
                child.userData.draggable = false;
                initialVisibilities[child.name] = false;
                initialDraggables[child.name] = false;
              }
            }
          }
        });

        model.userData.initialPositions = initialPositions;
        model.userData.initialVisibilities = initialVisibilities;
        model.userData.initialDraggables = initialDraggables;
        this.models[experiment] = model;
        if (this.state.selectedPrecipitate === experiment) {
          this.switchExperiment(experiment);
        }
      },
      undefined,
      (error) => {
        console.error(`Failed to load model for ${experiment}:`, error);
      }
    );
  }

  switchExperiment = (experiment) => {
    if (this.currentModel) {
      this.removeModelFromScene(this.currentModel);
      this.currentModel = null;
    }

    this.resetExperiment();

    const newModel = this.models[experiment];
    if (newModel) {
      const modelClone = newModel.clone();
      modelClone.userData.collide = newModel.userData.collide;
      modelClone.userData.initialPositions = { ...newModel.userData.initialPositions };
      modelClone.userData.initialVisibilities = { ...newModel.userData.initialVisibilities };
      modelClone.userData.initialDraggables = { ...newModel.userData.initialDraggables };

      modelClone.traverse((child) => {
        if (child.isMesh) {
          const initialPos = modelClone.userData.initialPositions[child.name];
          const initialVis = modelClone.userData.initialVisibilities[child.name];
          const initialDrag = modelClone.userData.initialDraggables[child.name];
          if (initialPos) {
            child.position.copy(initialPos);
          }
          child.rotation.set(0, 0, 0);
          child.visible = initialVis !== undefined ? initialVis : true;
          child.userData.draggable = initialDrag || false;

          if (experiment === "Combination_Reaction") {
            if (child.name === "magnesium") {
              child.userData.draggable = true;
            }
            if (child.name === "falsk_GasFilled" || child.name === "falsk_Gas") {
              child.material = new THREE.MeshStandardMaterial({
                map: child.material.map || null,
                metalness: 0.5,
                roughness: 0.1,
                envMap: this.scene.environment,
                side: THREE.DoubleSide,
                transparent: true,
                opacity: 0.1
              });
              child.renderOrder = 2;
              child.visible = false;
            }
            if (child.name === "pink") {
              child.material = new THREE.MeshStandardMaterial({
                color: 0xff69b4,
                metalness: 0.5,
                roughness: 0.5,
                envMap: this.scene.environment,
                transparent: false
              });
              child.renderOrder = 1;
              child.visible = false;
            }
            if (child.name === "phenolpthelin_sulpher") {
              child.userData.draggable = false;
            }
          } else if (experiment === "Decomposition_Reactions") {
            if (child.name === "falsk_Gas_exp6_1") {
              child.userData.draggable = true;
            }
            if (child.name === "ToothPick") {
              child.userData.draggable = false;
            }
          } else if (experiment === "Displacement_Reactions") {
            if (child.name === "lab_beaker_b_0002") {
              child.userData.draggable = true;
            }
            if (child.name === "falsk_Gas" || child.name === "falsk_Gas_exp6_3") {
              child.material = new THREE.MeshStandardMaterial({
                map: child.material.map || null,
                metalness: 0.5,
                roughness: 0.1,
                envMap: this.scene.environment,
                side: THREE.DoubleSide,
                transparent: true,
                opacity: 0.4
              });
              child.visible = false;
            }
          }
        }
      });

      this.scene.add(modelClone);
      this.currentModel = modelClone;
    }

    this.setState({
      combintaionState: experiment === "Combination_Reaction",
      combination_step1: false
    });
  }

  resetExperiment = () => {
    this.displayGas = false;
    this.toothPlace = false;
    this.beakerOnPlace = false;
    this.rotatePhenol = false;
    this.toothPick = null;
    this.beakerOnPlace2 = false;

    if (this.gas) {
      this.gas.update(0.0016, false);
      this.gas.points.visible = false;
    }
    if (this.fire2) {
      this.fire2.update(0.0016, false);
      this.fire2.points.visible = false;
    }

    this.setState({ combination_step1: false });
  }

  removeModelFromScene = (model) => {
    if (!model) return;

    model.traverse((obj) => {
      if (obj.isMesh) {
        if (obj.geometry) {
          obj.geometry.dispose();
        }
        if (obj.material) {
          if (Array.isArray(obj.material)) {
            obj.material.forEach(this.disposeMaterial);
          } else {
            this.disposeMaterial(obj.material);
          }
        }
      }
    });

    if (model.parent) {
      model.parent.remove(model);
    }
  }

  disposeMaterial = (material) => {
    if (material.map) material.map.dispose();
    if (material.lightMap) material.lightMap.dispose();
    if (material.bumpMap) material.bumpMap.dispose();
    if (material.normalMap) material.normalMap.dispose();
    if (material.specularMap) material.specularMap.dispose();
    if (material.envMap) material.envMap.dispose();
    material.dispose();
  }

  onPointerUp = () => {
    this.isDragging = false;
    document.body.style.cursor = "default";
    if (this.dragObject) {
      console.log("Drag Object: ", this.dragObject.name);
      console.log("Drag Object: ", this.dragObject.position);

      if (this.state.combintaionState) {
        if (this.dragObject.name === "magnesium") {
          this.dragObject.position.set(0.9214650899794408, 1.7017893144265375, -0.1357724517583847);
          this.displayGas = true;
        }
        if (this.state.combination_step1 && this.dragObject.name === "phenolpthelin_sulpher") {
          this.dragObject.position.set(1.7767806490906706, 1.419365549939675, -0.06561276316642761);
          this.rotatePhenol = true;
        }
      }
      if (this.state.selectedPrecipitate === "Decomposition_Reactions") {
        if (this.dragObject.name === "falsk_Gas_exp6_1") {
          this.dragObject.position.set(0.8253455171016113, 1.879702419976883, -0.09929551184177399);
          this.toothPlace = true;
        }
        if (this.dragObject.name === "ToothPick" && this.toothPlace) {
          this.toothPick = this.dragObject;
          this.dragObject.position.set(1.0269716364557153, 1.9937957495866314, -0.1587466597557068);
          this.toothPlace = false;
        }
      }
      if (this.state.selectedPrecipitate === "Displacement_Reactions") {
        if (this.dragObject.name === "lab_beaker_b_0002") {
          this.dragObject.position.set(0.8412901575786115, 1.819737519037023, -0.04929138720035553);
          this.beakerOnPlace = true;
        }
      }
      if (this.state.selectedPrecipitate === "Double_Displacement_Reactions") {
        if (this.dragObject.name === "lab_beaker_b_0002") {
          this.dragObject.position.set(1.3552053545502465, 1.3218348463566651, -0.04929138720035553);
          this.beakerOnPlace2 = true;
        }
      }

      this.dragObject.userData.isBeingDragged = false;
      this.dragObject = null;
    }
  }

  onPointerClick = (event) => {
    this.pointer.x = (event.clientX / window.innerWidth) * 2 - 1;
    this.pointer.y = -(event.clientY / window.innerHeight) * 2 + 1;
    this.raycaster.setFromCamera(this.pointer, this.camera);

    const draggableObjects = [];
    this.scene.traverse((child) => {
      if (child.userData.draggable) {
        draggableObjects.push(child);
      }
    });

    const intersects = this.raycaster.intersectObjects(draggableObjects, true);
    if (intersects.length > 0) {
      this.dragObject = intersects[0].object;
      this.dragObject.userData.isBeingDragged = true;
      this.isDragging = true;

      const worldPos = this.dragObject.position.clone();
      if (this.dragObject.parent) {
        this.dragObject.parent.localToWorld(worldPos);
      }
      this.dragPlane.position.copy(worldPos);
      this.dragPlane.lookAt(this.camera.position);

      const planeIntersects = this.raycaster.intersectObject(this.dragPlane);
      if (planeIntersects.length > 0) {
        this.initialDragOffset.subVectors(worldPos, planeIntersects[0].point);
      } else {
        this.initialDragOffset.set(0, 0, 0);
      }
    }
  }

  onMouseMove = (event) => {
    this.moveMouse.x = (event.clientX / window.innerWidth) * 2 - 1;
    this.moveMouse.y = -(event.clientY / window.innerHeight) * 2 + 1;
    this.raycaster.setFromCamera(this.moveMouse, this.camera);

    const draggableObjects = [];
    this.scene.traverse((child) => {
      if (child.userData.draggable) {
        draggableObjects.push(child);
      }
    });
    const intersects = this.raycaster.intersectObjects(draggableObjects, true);
    document.body.style.cursor = intersects.length > 0 ? "pointer" : "default";

    if (this.isDragging && this.dragObject) {
      const intersects = this.raycaster.intersectObject(this.dragPlane);
      if (intersects.length > 0) {
        const intersection = intersects[0].point;
        const newWorldPos = intersection.add(this.initialDragOffset);
        let localPos = newWorldPos.clone();
        if (this.dragObject.parent) {
          this.dragObject.parent.worldToLocal(localPos);
        }
        this.dragObject.position.x = localPos.x;
        this.dragObject.position.y = localPos.y;
      }
    }
  }

  setupEnvMap = () => {
    const hdriPath = '/Resources/Practical2-FlameTest/back2.hdr';
    new RGBELoader().load(hdriPath, (texture) => {
      texture.mapping = THREE.EquirectangularReflectionMapping;
      this.scene.background = texture;
      const pmremGenerator = new THREE.PMREMGenerator(this.renderer);
      this.scene.environment = pmremGenerator.fromEquirectangular(texture).texture;
      pmremGenerator.dispose();
    });
  }

  setupLight = () => {
    this.scene.add(new THREE.HemisphereLight(0xffffbb, 0x080820, 0.5));
    const directionalLight = new THREE.DirectionalLight(0xffffff, 2);
    directionalLight.position.set(5, 10, 5);
    directionalLight.castShadow = true;
    directionalLight.shadow.mapSize.width = 1024;
    directionalLight.shadow.mapSize.height = 1024;
    directionalLight.shadow.camera.near = 0.5;
    directionalLight.shadow.camera.far = 50;
    directionalLight.shadow.camera.left = -10;
    directionalLight.shadow.camera.right = 10;
    directionalLight.shadow.camera.top = 10;
    directionalLight.shadow.camera.bottom = -10;
    this.scene.add(directionalLight);
  }

  createDragPlane = () => {
    const planeGeometry = new THREE.PlaneGeometry(1000, 1000);
    const planeMaterial = new THREE.MeshBasicMaterial({ visible: false });
    this.dragPlane = new THREE.Mesh(planeGeometry, planeMaterial);
    this.scene.add(this.dragPlane);
  }

  initFire = () => {
    this.bunsenFlame = getBunsenFlame({
      camera: this.camera,
      emitter: { position: new THREE.Vector3(0.0818, 0.16, -0.01) },
      parent: this.scene,
      rate: 80,
      texture: "./Resources/fire.png",
      colors: new THREE.Vector4(0.8, 0.8, 0.8, 0.6),
      initialScale: { x: 0.099, y: 0.0099, z: 0.09 },
      objectsToCollide: this.objectsToCollide
    });

    this.gas = getBunsenFlame({
      camera: this.camera,
      emitter: { position: new THREE.Vector3(0.084, 0.175, -0.005) },
      parent: this.scene,
      rate: 20,
      texture: "./Resources/fire.png",
      colors: new THREE.Vector4(1.5, 1.8, 0.5, 0.2),
      initialScale: { x: 0.15, y: 0.02, z: 0.02 },
      objectsToCollide: this.objectsToCollide
    });

    this.fire2 = getBunsenFlame({
      camera: this.camera,
      emitter: { position: new THREE.Vector3(0.08189, 0.1955, -0.01) },
      parent: this.scene,
      rate: 80,
      texture: "./Resources/fire.png",
      colors: new THREE.Vector4(0.8, 0.8, 0.8, 0.6),
      initialScale: { x: 0.099, y: 0.0099, z: 0.09 },
      objectsToCollide: this.objectsToCollide
    });

    this.bunsenFlame.points.material.depthTest = false;
    this.bunsenFlame.points.material.depthWrite = true;
    this.gas.points.material.depthTest = false;
    this.gas.points.material.depthWrite = true;
    this.gas.points.visible = false;
    this.fire2.points.material.depthTest = false;
    this.fire2.points.material.depthWrite = true;
    this.fire2.points.visible = false;
  }

  setupCamera = () => {
    this.camera = new THREE.PerspectiveCamera(60, (this.WNDSIZE.width / this.WNDSIZE.height), 0.1, 10000);
    this.camera.position.set(0.142312588411287, 0.16209877756769583, 0.17036742976745373);
    this.camera.lookAt(this.scene.position);

    this.controls = new OrbitControls(this.camera, this.renderer.domElement);
    this.controls.target.set(0.13593197914760535, 0.16209877756769583, -0.014584321974646685);
    this.controls.enablePan = false;
    this.controls.enableZoom = false;
    this.controls.enableRotate = false;
    this.controls.minDistance = 0.01;
    this.controls.maxDistance = 500;
    this.controls.update();
  }

  start = () => {
    if (!this.frameId) {
      this.frameId = requestAnimationFrame(this.update);
    }
  }

  stop = () => {
    cancelAnimationFrame(this.frameId);
  }

  resizeWindow = () => {
    this.WNDSIZE.width = this.mount.clientWidth;
    this.WNDSIZE.height = this.mount.clientHeight;
    this.camera.aspect = this.WNDSIZE.width / this.WNDSIZE.height;
    this.camera.updateProjectionMatrix();
    this.renderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
  }

  handlePrecipitateChange = (event) => {
    const newPrecipitate = event.target.value;
    this.setState({ selectedPrecipitate: newPrecipitate }, () => {
      this.switchExperiment(newPrecipitate);
    });
  }

  update = () => {
    const deltaTime = this.clock.getDelta();
    if (this.bunsenFlame) {
      this.bunsenFlame.update(deltaTime, true);
    }

    // Combination_Reaction
    if (this.state.combintaionState && this.currentModel) {
      if (this.displayGas && !this.state.combination_step1) {
        if (this.gas) {
          this.gas.points.visible = true;
          this.gas.update(0.0016, true);
          setTimeout(() => {
            this.displayGas = false;
            this.gas.points.visible = false;
            this.gas.update(0.0016, false);
            this.setState({ combination_step1: true });
          }, 4000);
        }
      }

      if (this.state.combination_step1) {
        if (this.gas) {
          this.gas.points.visible = false;
          this.gas.update(0.0016, false);
        }
        this.currentModel.traverse((child) => {
          if (child.isMesh) {
            if (child.name === "magnesium") {
              child.visible = false;
            }
            if (child.name === "falsk_GasFilled") {
              child.renderOrder = 2;
              child.visible = true;
              child.material = new THREE.MeshStandardMaterial({
                map: child.material.map || null,
                metalness: 0.5,
                roughness: 0.1,
                envMap: this.scene.environment,
                side: THREE.DoubleSide,
                transparent: true,
                opacity: 0.4,
                depthWrite: false,
                depthTest: true
              });
            }
            if (child.name === "phenolpthelin_sulpher") {
              child.userData.draggable = true;
            }
          }
        });

        if (this.rotatePhenol) {
          this.currentModel.traverse((child) => {
            if (child.name === "phenolpthelin_sulpher") {
              if (child.rotation.z <= 0.8) {
                child.rotation.z += 0.01;
                console.log("rotation", child.rotation.z);
              }
              if (child.rotation.z >= 0.8) {
                child.visible = false;
              }
            }
            if (child.name === "pink") {
              child.renderOrder = 1;
              child.material = new THREE.MeshStandardMaterial({
                color: 0xff69b4,
                metalness: 0.5,
                roughness: 0.5,
                envMap: this.scene.environment,
                transparent: false
              });
              child.visible = true;
            }
          });
        }
      }
    } else {
      if (this.gas && !this.toothPlace && !this.beakerOnPlace) {
        this.gas.points.visible = false;
        this.gas.update(0.0016, false);
      }
    }

    // Decomposition_Reactions
    if (this.state.selectedPrecipitate === "Decomposition_Reactions" && this.currentModel) {
      if (this.toothPlace) {
        this.currentModel.traverse((child) => {
          if (child.name === "falsk_Gas_exp6_1") {
            child.userData.draggable = false;
          }
          if (child.name === "ToothPick") {
            child.userData.draggable = true;
          }
        });

        if (this.gas) {
          this.gas.points.visible = true;
          this.gas.update(0.0016, true);
          setTimeout(() => {
            this.toothPlace = false;
            this.gas.points.visible = false;
            this.gas.update(0.0016, false);
          }, 4000);
        }
      } else {
        this.currentModel.traverse((child) => {
          if (child.name === "falsk_Gas_exp6_1") {
            child.userData.draggable = true;
          }
          if (child.name === "ToothPick") {
            child.userData.draggable = false;
          }
        });
        if (this.toothPick) {
          if (
            this.toothPick.position.x === 1.0269716364557153 &&
            this.toothPick.position.y >= 1.9710812262989656
          ) {
            console.log("on Plase");
            if (this.fire2) {
              this.fire2.points.visible = true;
              this.fire2.update(0.0016, true);
            }
          } else {
            if (this.fire2) {
              this.fire2.points.visible = false;
              this.fire2.update(0.0016, false);
            }
          }
        } else {
          if (this.fire2) {
            this.fire2.points.visible = false;
            this.fire2.update(0.0016, false);
          }
        }
      }
    } else {
      if (this.fire2) {
        this.fire2.points.visible = false;
        this.fire2.update(0.0016, false);
      }
      if (this.gas && !this.displayGas && !this.beakerOnPlace) {
        this.gas.points.visible = false;
        this.gas.update(0.0016, false);
      }
    }

    // Displacement_Reactions
    if (this.state.selectedPrecipitate === "Displacement_Reactions" && this.currentModel) {
      this.currentModel.traverse((child) => {
        if (child.name === "lab_beaker_b_0002") {
          child.userData.draggable = true;
        }
      });

      if (this.beakerOnPlace) {
        if (this.gas) {
          this.gas.points.visible = true;
          this.gas.update(0.0016, true);
        }
        this.currentModel.traverse((child) => {
          if (child.name === "falsk_Gas" || child.name === "falsk_Gas_exp6_3") {
            child.visible = true;
            child.material = new THREE.MeshStandardMaterial({
              map: child.material.map || null,
              metalness: 0.5,
              roughness: 0.1,
              envMap: this.scene.environment,
              side: THREE.DoubleSide,
              transparent: true,
              opacity: 0.4,
              depthWrite: false,
              depthTest: true
            });
          }
        });
      } else {
        if (this.gas && !this.displayGas && !this.toothPlace) {
          this.gas.points.visible = false;
          this.gas.update(0.0016, false);
        }
        this.currentModel.traverse((child) => {
          if (child.name === "falsk_Gas" || child.name === "falsk_Gas_exp6_3") {
            child.visible = false;
          }
        });
      }
    } else {
      if (this.gas && !this.displayGas && !this.toothPlace) {
        this.gas.points.visible = false;
        this.gas.update(0.0016, false);
      }
    }

    // Double_Displacement_Reactions
    if (this.state.selectedPrecipitate === "Double_Displacement_Reactions" && this.currentModel) {
      this.currentModel.traverse((child) => {
        if (this.beakerOnPlace2) {
          if (child.name === "yellow") {
            child.visible = true;
          }
          if (child.name === "lab_beaker_b_0002") {
            if (child.rotation.z >= -0.8) {
              child.rotation.z -= 0.01;
              console.log("rotation", child.rotation.z);
            }
            if (child.rotation.z <= -0.8) {
              child.visible = false;
            }
          }
        } else {
          if (child.name === "yellow") {
            child.visible = false;
          }
        }
      });
    }

    if (this.controls) {
      this.controls.update();
    }
    this.renderScene();
    this.frameId = window.requestAnimationFrame(this.update);
  }

  renderScene = () => {
    if (this.renderer && this.scene && this.camera) {
      this.renderer.render(this.scene, this.camera);
    }
  }

  render() {
    const info = Precipitate_Info[this.state.selectedPrecipitate] || {};
    return (
      <div className="canvasContainer" style={{ position: 'relative' }}>
        <div className="ExpTitle">Title: Reactions</div>
        <select
          value={this.state.selectedPrecipitate}
          onChange={this.handlePrecipitateChange}
          style={{ position: 'absolute', top: '10%', right: '1%', zIndex: 100 }}
        >
          <option value="Combination_Reaction">Combination Reactions (Synthesis)</option>
          <option value="Decomposition_Reactions">Decomposition Reactions</option>
          <option value="Displacement_Reactions">Displacement Reactions (Single Replacement)</option>
          <option value="Double_Displacement_Reactions">Double Displacement Reactions</option>
        </select>
        <div
          style={{
            position: 'absolute',
            top: '20%',
            left: '1%',
            height: '45%',
            width: '24%',
            padding: '25px',
            display: 'flex',
            flexDirection: 'column',
            justifyContent: 'center',
            background: 'rgba(0, 0, 0, 0.7)',
            color: 'white',
            borderRadius: '8px',
            zIndex: 100,
            fontFamily: 'Arial, sans-serif',
            fontSize: '14px',
            lineHeight: '1.5',
            boxShadow: '0 0 10px rgba(0,0,0,0.3)'
          }}
        >
          <h3 style={{ margin: '0 0 10px 0', fontSize: '18px' }}>{info.title || 'Reaction Information'}</h3>
          <p style={{ margin: '5px 0' }}><strong>Description:</strong> {info.description || 'Select a reaction to see details'}</p>
          <br/>
          <p style={{ margin: '5px 0' }}><strong>Example:</strong> {info.example || 'No example available'}</p>
          <br/>
          <p style={{ margin: '5px 0' }}><strong>Characteristics:</strong></p>
          <pre style={{ whiteSpace: 'pre-wrap', margin: '0' }}>{info.characteristics || 'No characteristics available'}</pre>
          <br/>
          <p style={{ margin: '5px 0' }}><strong>Steps:</strong></p>
          <pre style={{ whiteSpace: 'pre-wrap', margin: '0' }}>{info.steps || 'No characteristics available'}</pre>
          

        </div>
        <div ref={ref => (this.mount = ref)} className="canvasContainer__maincanvas"/>
      </div>
    );
  }
}

export default ReactionsExperiment;