import React, { Component } from 'react';
import * as THREE from "three";

import { RGBELoader } from 'three/examples/jsm/loaders/RGBELoader.js';
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js';
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js';
import { getBunsenFlame } from './getBunsenFlame.js';

const Precipate_Color = {
    Led_chromate: 0xffff00,      // Yellow
    Ferric_hydroxide: 0x8b4513, // Brown
    Nickel_hydroxide: 0x00ff00, // Green
    Lead_iodide: 0xffff33,      // Bright Yellow
    Iron_chloride: 0x4A0404,    // Dark Red (adjusted from Steel Blue)
    zinc_hydroxide: 0xffffff    // White
};

const Precipitate_Info = {
    Led_chromate: {
        formula: "Pb²⁺ + CrO₄²⁻ → PbCrO₄",
        solution: "Lead(II) ions react with chromate ions to form a precipitate.",
        colorName: "Yellow"
    },
    Ferric_hydroxide: {
        formula: "Fe³⁺ + 3OH⁻ → Fe(OH)₃",
        solution: "Iron(III) ions precipitate with hydroxide ions in basic solution.",
        colorName: "Brown"
    },
    Nickel_hydroxide: {
        formula: "Ni²⁺ + 2OH⁻ → Ni(OH)₂",
        solution: "Nickel(II) ions form a precipitate with hydroxide ions.",
        colorName: "Green"
    },
    Lead_iodide: {
        formula: "Pb²⁺ + 2I⁻ → PbI₂",
        solution: "Lead(II) ions react with iodide ions in solution.",
        colorName: "Bright Yellow"
    },
    Iron_chloride: {
        formula: "Fe³⁺ + 3OH⁻ → Fe(OH)₃", // Adjusted to Fe(OH)₃ as FeCl₃ is soluble
        solution: "Iron(III) ions precipitate as hydroxide in basic conditions.",
        colorName: "Dark Brown"
    },
    zinc_hydroxide: {
        formula: "Zn²⁺ + 2OH⁻ → Zn(OH)₂",
        solution: "Zinc(II) ions form a precipitate with hydroxide ions.",
        colorName: "White"
    }
};

class IonPrecipitate extends Component {
    constructor(props) {
        super(props);
        this.scene = null;
        this.camera = null;
        this.renderer = null;
        this.cube = null;
        this.light = null;
        this.frameId = null;
        this.controls = null;

        this.raycaster = null;
        this.pointer = null;
        this.moveMouse = new THREE.Vector2();
        this.lastTime = performance.now();

        this.dragPlane = null;
        this.initialDragOffset = new THREE.Vector3();
        this.previousMousePosition = new THREE.Vector2();
        this.dragStartPosition = new THREE.Vector3();
        this.draggableObjects = [];

        this.textureLoader = new THREE.TextureLoader();
        this.cubeTextureLoader = new THREE.CubeTextureLoader();
        this.rgbLoader = new RGBELoader();
        this.expModel = null;
        this.gltfLoader = null;
        this.smokeEffect = null;
        this.Precipate = null;
        this.precipitateMaterial = null;

        this.bunsenFlame = null;
        this.onPlaceFlag = false;
        this.solModel = null;
        this.initialSolPosition = new THREE.Vector3();
        this.timeoutId = null;

        this.WNDSIZE = { width: 0, height: 0 };

        this.state = {
            applyGlass: false,
            selectedPrecipitate: "Combination_Reaction",
            showInfo: false
        };
    }

    // Convert hex to RGBA
    hexToRGBA = (hex, alpha = 0.3) => {
        const r = (hex >> 16) & 255;
        const g = (hex >> 8) & 255;
        const b = hex & 255;
        return `rgba(${r}, ${g}, ${b}, ${alpha})`;
    };

    setupModel = () => {
        this.gltfLoader.load(
            'Resources/Practical5-IonPrecipitate/chemExp.glb',
            (gltf) => {
                this.expModel = gltf.scene;
                this.expModel.traverse((child) => {
                    if (child.isMesh) {
                        child.material = new THREE.MeshStandardMaterial({
                            map: child.material.map || null,
                            metalness: 0.5,
                            roughness: 0.1,
                            envMap: this.scene.environment,
                            side: THREE.DoubleSide,
                        });
                    }
                });
                this.expModel.position.set(0, 0, 0);
                this.expModel.scale.set(0.1, 0.1, 0.1);
                this.scene.add(this.expModel);
            }
        );

        this.gltfLoader.load(
            'Resources/Practical5-IonPrecipitate/solutions.glb',
            (gltf) => {
                this.expModel = gltf.scene;
                this.expModel.traverse((child) => {
                    if (child.isMesh) {
                        child.material = new THREE.MeshStandardMaterial({
                            map: child.material.map || null,
                            metalness: 0.5,
                            roughness: 0.1,
                            envMap: this.scene.environment,
                            side: THREE.DoubleSide,
                            transparent: true,
                            opacity: 0.5
                        });
                        if (child.name === "Precipate") {
                            child.userData.draggable = false;
                            this.Precipate = child;
                            this.precipitateMaterial = new THREE.MeshStandardMaterial({
                                map: child.material.map || null,
                                color: Precipate_Color[this.state.selectedPrecipitate],
                                metalness: 0.5,
                                roughness: 0.1,
                                envMap: this.scene.environment,
                                side: THREE.DoubleSide,
                                transparent: true,
                                opacity: 1.5
                            });
                            child.material = this.precipitateMaterial;
                        }
                        child.userData.draggable = true;
                    }
                });
                this.Precipate.visible = false;
                this.expModel.position.set(0, 0, 0);
                this.expModel.scale.set(0.1, 0.1, 0.1);
                this.scene.add(this.expModel);

                this.expModel.traverse((child) => {
                    if (child.name === "sol1") {
                        this.solModel = child;
                        this.initialSolPosition.copy(child.position);
                    }
                });
            }
        );
    };

    updatePrecipitateColor = () => {
        if (this.precipitateMaterial) {
            this.precipitateMaterial.color.setHex(Precipate_Color[this.state.selectedPrecipitate]);
        }
    };

    resetSimulation = () => {
        this.onPlaceFlag = false;
        this.Precipate.visible = false;
        if (this.solModel) {
            this.solModel.position.copy(this.initialSolPosition);
            this.solModel.userData.draggable = true;
            this.solModel.traverse((child) => {
                if (child.isMesh && child.name === "Precipate") {
                    child.visible = false;
                }
            });
        }
        if (this.timeoutId) {
            clearTimeout(this.timeoutId);
            this.timeoutId = null;
        }
        this.updatePrecipitateColor();
        this.setState({ showInfo: false });
    };

    handlePrecipitateChange = (event) => {
        const newPrecipitate = event.target.value;
        this.setState({ selectedPrecipitate: newPrecipitate }, () => {
            this.resetSimulation();
        });
    };

    onPointerUp = () => {
        this.isDragging = false;
        document.body.style.cursor = "default";

        if (this.dragObject) {
            console.log("Dropped object:", this.dragObject.name);

            if (this.dragObject.name === "sol1") {
                if (!this.onPlaceFlag) {
                    this.dragObject.position.set(0.8320579144987983, 1.5555229508145298, -0.12362837858764053);
                    this.solModel = this.dragObject;
                    this.onPlaceFlag = true;

                    this.timeoutId = setTimeout(() => {
                        this.Precipate.visible = true;
                        this.updatePrecipitateColor();

                        this.solModel.position.set(1.2178195441628779, 1.1758987190613595, -0.11077136885951745);
                        this.solModel.userData.draggable = false;
                        this.solModel.traverse((child) => {
                            if (child.isMesh && child.name === "Precipate") {
                                child.visible = true;
                            }
                        });
                        this.onPlaceFlag = false;
                        this.setState({ showInfo: true });
                    }, 2000);
                }
            }
            this.dragObject = null;
        }
    };

    onPointerClick = (event) => {
        this.pointer.x = (event.clientX / window.innerWidth) * 2 - 1;
        this.pointer.y = -(event.clientY / window.innerHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.pointer, this.camera);

        const draggableObjects = [];
        this.scene.traverse((child) => {
            if (child.userData.draggable) {
                draggableObjects.push(child);
            }
        });

        const intersects = this.raycaster.intersectObjects(draggableObjects, true);

        if (intersects.length > 0) {
            const target = intersects[0].object;
            this.dragObject = target;
            this.dragStartPosition.copy(this.dragObject.position);

            if (this.fadeOutTimeout) {
                clearTimeout(this.fadeOutTimeout);
            }

            this.setState({
                selectedStick: target.name,
                lastSelectedStick: target.name,
                infoVisible: true,
            });

            const worldPos = this.dragObject.position.clone();
            if (this.dragObject.parent) {
                this.dragObject.parent.localToWorld(worldPos);
            }
            this.dragPlane.position.copy(worldPos);
            this.dragPlane.lookAt(this.camera.position);

            this.raycaster.setFromCamera(this.pointer, this.camera);
            const planeIntersects = this.raycaster.intersectObject(this.dragPlane);
            if (planeIntersects.length > 0) {
                const intersection = planeIntersects[0].point;
                this.initialDragOffset.subVectors(worldPos, intersection);
            } else {
                this.initialDragOffset.set(0, 0, 0);
            }

            this.isDragging = true;
            console.log("Selected draggable object:", this.dragObject.name);
        } else {
            console.log("No draggable object selected.");
        }
    };

    onMouseMove = (event) => {
        this.moveMouse.x = (event.clientX / window.innerWidth) * 2 - 1;
        this.moveMouse.y = -(event.clientY / window.innerHeight) * 2 + 1;

        this.raycaster.setFromCamera(this.moveMouse, this.camera);

        const draggableObjects = [];
        this.scene.traverse((child) => {
            if (child.userData.draggable) {
                draggableObjects.push(child);
            }
        });
        const intersects = this.raycaster.intersectObjects(draggableObjects, true);

        if (intersects.length > 0) {
            document.body.style.cursor = "pointer";
        } else {
            document.body.style.cursor = "default";
        }

        this.dragObjectFunc(event);
    };

    dragObjectFunc = (event) => {
        if (this.isDragging && this.dragObject) {
            this.raycaster.setFromCamera(this.moveMouse, this.camera);
            const intersects = this.raycaster.intersectObject(this.dragPlane);

            if (intersects.length > 0) {
                const intersection = intersects[0].point;
                const newWorldPos = intersection.add(this.initialDragOffset);

                let localPos = newWorldPos.clone();
                if (this.dragObject.parent) {
                    this.dragObject.parent.worldToLocal(localPos);
                }

                this.dragObject.position.x = localPos.x;
                this.dragObject.position.y = localPos.y;

                console.log("Dragging object:", this.dragObject.name, "to position:", localPos);
            }
        }
    };

    componentDidMount() {
        this.initialize();
    }

    componentWillUnmount() {
        this.stop();
        window.removeEventListener('mousedown', this.onPointerClick);
        window.removeEventListener('mouseup', this.onPointerUp);
        window.removeEventListener('mousemove', this.onMouseMove);
        window.removeEventListener("resize", this.resizeWindow);
    }

    setupEnvMap = () => {
        const hdriPath = '/Resources/Practical2-FlameTest/back.hdr';
        new RGBELoader().load(hdriPath, (texture) => {
            texture.mapping = THREE.EquirectangularReflectionMapping;
            this.scene.background = texture;
            const pmremGenerator = new THREE.PMREMGenerator(this.renderer);
            this.scene.environment = pmremGenerator.fromEquirectangular(texture).texture;
            pmremGenerator.dispose();
        });
    };

    initialize = () => {
        this.scene = new THREE.Scene();
        this.WNDSIZE.width = this.mount.clientWidth;
        this.WNDSIZE.height = this.mount.clientHeight;

        this.renderer = new THREE.WebGLRenderer({ antialias: true, alpha: true });
        this.renderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
        this.renderer.toneMapping = THREE.ACESFilmicToneMapping;
        this.renderer.toneMappingExposure = 0.4;
        this.renderer.shadowMap.enabled = true;
        this.renderer.shadowMap.type = THREE.PCFSoftShadowMap;
        this.renderer.outputEncoding = THREE.sRGBEncoding;

        this.mount.appendChild(this.renderer.domElement);

        this.raycaster = new THREE.Raycaster();
        this.pointer = new THREE.Vector2();
        this.gltfLoader = new GLTFLoader();

        this.clock = new THREE.Clock();

        this.light = new THREE.HemisphereLight(0xffffbb, 0x080820, 1);
        this.scene.add(this.light);

        this.setupCamera();
        this.renderScene();
        this.initFire();
        this.setupLight();
        this.createDragPlane();
        this.setupEnvMap();
        this.setupModel();

        this.start();

        window.addEventListener('mousedown', this.onPointerClick);
        window.addEventListener('mouseup', this.onPointerUp);
        window.addEventListener('mousemove', this.onMouseMove);
        window.addEventListener("resize", this.resizeWindow);
    };

    createDragPlane = () => {
        const planeGeometry = new THREE.PlaneGeometry(1000, 1000);
        const planeMaterial = new THREE.MeshBasicMaterial({ visible: false });
        this.dragPlane = new THREE.Mesh(planeGeometry, planeMaterial);
        this.scene.add(this.dragPlane);
    };

    initFire = () => {
        let { renderer, scene, camera, fireSourse } = this;
        let position;
        if (fireSourse) {
            position = fireSourse.position.clone();
        } else {
            position = new THREE.Vector3(0.0, 0.0, 0.0);
        }

        this.bunsenFlame = getBunsenFlame({
            camera: this.camera,
            emitter: { position: new THREE.Vector3(0.0818, 0.136, -0.0134) },
            parent: this.scene,
            rate: 80,
            texture: "./Resources/fire.png",
            colors: new THREE.Vector4(0.8, 0.8, 0.8, 0.6),
            initialScale: { x: 0.06, y: 0.0065, z: 0.02 },
            objectsToCollide: this.objectsToCollide
        });

        this.bunsenFlame.points.material.depthTest = false;
        this.bunsenFlame.points.material.depthWrite = true;
    };

    setupLight = () => {
        const ambientLight = new THREE.AmbientLight(0xffffff, 0.2);
        this.scene.add(ambientLight);

        const directionalLight = new THREE.DirectionalLight(0xffffff, 0.6);
        directionalLight.position.set(0.5, 1.0, 0.5);
        directionalLight.castShadow = false;
        this.scene.add(directionalLight);

        const pointLight = new THREE.PointLight(0xffffff, 0.8, 0.7);
        pointLight.position.set(0.1, 0.2, 0.05);
        this.scene.add(pointLight);

        const pointLight2 = new THREE.PointLight(0xffffff, 0.5, 0.7);
        pointLight2.position.set(0.25, 0.2, 0.03);
        this.scene.add(pointLight2);
    };

    setupCamera = () => {
        this.camera = new THREE.PerspectiveCamera(60, (this.WNDSIZE.width / this.WNDSIZE.height), 0.1, 10000);
        this.camera.position.set(0.142312588411287, 0.16209877756769583, 0.17036742976745373);
        this.camera.lookAt(this.scene.position);

        this.controls = new OrbitControls(this.camera, this.renderer.domElement);
        this.controls.target.set(0.13593197914760535, 0.16209877756769583, -0.014584321974646685);
        this.controls.enablePan = false;
        this.controls.enableZoom = false;
        this.controls.enableRotate = false;

        this.controls.minDistance = 0.01;
        this.controls.maxDistance = 500;
        this.controls.maxPolarAngle = Math.PI / 2;
        this.controls.update();
    };

    start = () => {
        if (!this.frameId) {
            this.frameId = requestAnimationFrame(this.update);
        }
    };

    stop = () => {
        cancelAnimationFrame(this.frameId);
    };

    resizeWindow = () => {
        this.WNDSIZE.width = this.mount.clientWidth;
        this.WNDSIZE.height = this.mount.clientHeight;

        this.camera.aspect = this.WNDSIZE.width / this.WNDSIZE.height;
        this.camera.updateProjectionMatrix();

        this.renderer.setSize(this.WNDSIZE.width, this.WNDSIZE.height);
    };

    update = () => {
        const deltaTime = this.clock.getDelta();
        this.bunsenFlame.update(deltaTime, true);
        this.controls.update();
        this.renderScene();
        this.frameId = window.requestAnimationFrame(this.update);
    };

    renderScene = () => {
        let { renderer, scene, camera } = this;
        if (renderer) {
            renderer.render(scene, camera);
        }
    };

    render() {
        const info = Precipitate_Info[this.state.selectedPrecipitate];
        const bgColor = this.hexToRGBA(Precipate_Color[this.state.selectedPrecipitate], 0.3);

        return (
            <div className="canvasContainer">
                <div className="ExpTitle">Title:  Mixture of some anions and cations that produce a precipitate</div>

                <select
                    value={this.state.selectedPrecipitate}
                    onChange={this.handlePrecipitateChange}
                    style={{ position: 'absolute', top: '10%', right: '1%', zIndex: 100 }}
                >
                    <option value="Led_chromate">Lead Chromate</option>
                    <option value="Ferric_hydroxide">Ferric Hydroxide</option>
                    <option value="Nickel_hydroxide">Nickel Hydroxide</option>
                    <option value="Lead_iodide">Lead Iodide</option>
                    <option value="Iron_chloride">Iron Chloride</option>
                    <option value="zinc_hydroxide">Zinc Hydroxide</option>
                </select>
                {this.state.showInfo && (
                    <div
                        className="precipitate-info"
                        style={{
                            height: '10%',
                            width: '25%',
                            position: 'absolute',
                            top: '10%',
                            right: '1%',
                            transform: 'translateX(-50%)',
                            color: '#fff',
                            backgroundColor: bgColor,
                            backdropFilter: 'blur(5px)', // Glass effect
                            padding: '15px 20px',
                            borderRadius: '5px',
                            fontSize: '16px',
                            fontFamily: 'Arial, sans-serif',
                            textAlign: 'center',
                            zIndex: 100,
                            animation: 'fadeIn 0.5s ease-in',
                            boxShadow: '0 4px 6px rgba(0, 0, 0, 0.1)' // Subtle shadow
                        }}
                    >
                        <div><strong>Formula:</strong> {info.formula}</div>
                        <div><strong>Solution:</strong> {info.solution}</div>
                        <div><strong>Precipitate Color:</strong> {info.colorName}</div>
                    </div>
                )}
                <div ref={ref => (this.mount = ref)} className="canvasContainer__maincanvas" />
                <style>{`
                    @keyframes fadeIn {
                        from { opacity: 0; }
                        to { opacity: 1; }
                    }
                `}</style>
            </div>
        );
    }
}

export default IonPrecipitate;