import * as THREE from 'three';

const _VS = `
uniform float pointMultiplier;

attribute float size;
attribute float angle;
attribute vec4 aColor;

uniform vec4 oColor;
varying vec4 vColor;
varying vec2 vAngle;

void main() {
  vec4 mvPosition = modelViewMatrix * vec4(position, 1.0);

  gl_Position = projectionMatrix * mvPosition;
  gl_PointSize = size * pointMultiplier / gl_Position.w;

  vAngle = vec2(cos(angle), sin(angle));
  vColor = oColor;
}`;

const _FS = `
uniform sampler2D diffuseTexture;

varying vec4 vColor;
varying vec2 vAngle;

void main() {
  vec2 coords = (gl_PointCoord - 0.5) * mat2(vAngle.x, vAngle.y, -vAngle.y, vAngle.x) + 0.5;
  gl_FragColor = texture2D(diffuseTexture, coords) * vColor;
}`;
function getBunsenFlame(params) {
  const { 
    camera, 
    emitter, 
    parent, 
    rate = 100, 
    texture, 
    colors, 
    initialScale = { x: 1.0, y: 1.0, z: 1.0 },
    objectsToCollide = []
  } = params;

  const uniforms = {
    diffuseTexture: { value: new THREE.TextureLoader().load(texture) },
    pointMultiplier: { value: window.innerHeight / (2.0 * Math.tan(30.0 * Math.PI / 180.0)) },
    oColor: { value: colors }
  };

  const material = new THREE.ShaderMaterial({
    uniforms: uniforms,
    vertexShader: _VS,
    fragmentShader: _FS,
    blending: THREE.AdditiveBlending,
    depthTest: false,
    depthWrite: true,
    transparent: true,
    vertexColors: true
  });

  let particles = [];
  let currentScale = { x: initialScale.x, y: initialScale.y, z: initialScale.z };
  let collisionDetected = false;
  let collidingObject = null;
  let emitterPosition = emitter.position.clone(); // Store emitter position

  const geometry = new THREE.BufferGeometry();
  geometry.setAttribute('position', new THREE.Float32BufferAttribute([], 3));
  geometry.setAttribute('size', new THREE.Float32BufferAttribute([], 1));
  geometry.setAttribute('aColor', new THREE.Float32BufferAttribute([], 4));
  geometry.setAttribute('angle', new THREE.Float32BufferAttribute([], 1));

  const points = new THREE.Points(geometry, material);
  points.frustumCulled = false;
  parent.add(points);

  const baseRadius = 0.03;
  const maxHeight = 0.3;
  const maxLife = 0.8;

  let emissionAccumulator = 0.0;

  function _addParticles(timeElapsed) {
    emissionAccumulator += timeElapsed;
    const particlesToAdd = Math.floor(emissionAccumulator * rate);
    emissionAccumulator -= particlesToAdd / rate;

    for (let i = 0; i < particlesToAdd; i++) {
      const life = (Math.random() * 0.5 + 0.5) * maxLife;
      const heightFactor = Math.random();
      const radiusFactor = 1.0 - (heightFactor * 0.7);

      const particle = {
        position: new THREE.Vector3(
          (Math.random() - 0.5) * baseRadius * radiusFactor * currentScale.x,
          heightFactor * maxHeight * currentScale.y,
          (Math.random() - 0.5) * baseRadius * radiusFactor * currentScale.z
        ).add(emitterPosition), // Use dynamic emitter position
        baseSize: 0.05 + Math.random() * 0.05,
        size: (0.05 + Math.random() * 0.05) * Math.max(currentScale.x, currentScale.z),
        color: new THREE.Color(uniforms.oColor.value.x, uniforms.oColor.value.y, uniforms.oColor.value.z),
        alpha: uniforms.oColor.value.w,
        life: life,
        maxLife: life,
        rotation: Math.random() * Math.PI * 2,
        rotationRate: (Math.random() - 0.5) * 0.02,
        velocity: new THREE.Vector3(
          (Math.random() - 0.5) * 0.1 * currentScale.x,
          (1.5 + Math.random() * 0.5) * currentScale.y,
          (Math.random() - 0.5) * 0.1 * currentScale.z
        )
      };
      particles.push(particle);
    }
  }

  function _updateGeometry() {
    const positions = [];
    const sizes = [];
    const colors = [];
    const angles = [];

    for (let p of particles) {
      positions.push(p.position.x, p.position.y, p.position.z);
      colors.push(p.color.r, p.color.g, p.color.b, p.alpha);
      sizes.push(p.size);
      angles.push(p.rotation);
    }

    geometry.setAttribute('position', new THREE.Float32BufferAttribute(positions, 3));
    geometry.setAttribute('size', new THREE.Float32BufferAttribute(sizes, 1));
    geometry.setAttribute('aColor', new THREE.Float32BufferAttribute(colors, 4));
    geometry.setAttribute('angle', new THREE.Float32BufferAttribute(angles, 1));

    geometry.attributes.position.needsUpdate = true;
    geometry.attributes.size.needsUpdate = true;
    geometry.attributes.aColor.needsUpdate = true;
    geometry.attributes.angle.needsUpdate = true;
  }

  function _checkCollisions() {
    collisionDetected = false;
    collidingObject = null;

    const step = Math.floor(particles.length / 1000) || 1;
    for (let i = 0; i < particles.length; i += step) {
      const particlePos = particles[i].position;

      for (const object of objectsToCollide) {
        if (!object.userData.collide || !object.userData.draggable) continue;

        if (!object.boundingSphere) {
          const boundingBox = new THREE.Box3().setFromObject(object);
          const center = boundingBox.getCenter(new THREE.Vector3());
          const size = boundingBox.getSize(new THREE.Vector3());
          const radius = Math.max(size.x, size.y, size.z) * 0.5;
          object.boundingSphere = new THREE.Sphere(center, radius);
        }
        object.boundingSphere.center.copy(object.position);

        if (object.boundingSphere.containsPoint(particlePos)) {
          collisionDetected = true;
          collidingObject = object;
          break;
        }
      }
      if (collisionDetected) break;
    }
  }

  function _updateParticles(timeElapsed) {
    particles.forEach(p => {
      p.life -= timeElapsed;
      const t = 1.0 - p.life / p.maxLife;
      p.alpha = Math.max(0.3, Math.sin(Math.PI * t));

      p.position.add(p.velocity.clone().multiplyScalar(timeElapsed));
      p.rotation += p.rotationRate;

      p.velocity.x *= 0.98;
      p.velocity.z *= 0.98;

      p.color.setRGB(uniforms.oColor.value.x, uniforms.oColor.value.y, uniforms.oColor.value.z);
      p.alpha = uniforms.oColor.value.w;
    });

    particles = particles.filter(p => p.life > 0);

    if (camera) {
      particles.sort((a, b) => {
        const d1 = camera.position.distanceTo(a.position);
        const d2 = camera.position.distanceTo(b.position);
        return d2 - d1;
      });
    }
  }

  function update(timeElapsed, show) {
    if (show) {
      _addParticles(timeElapsed);
    }
    _updateParticles(timeElapsed);
    _checkCollisions();
    _updateGeometry();
    points.material.needsUpdate = true;
  }

  function setScale(scale) {
    currentScale = {
      x: Math.max(0.01, scale.x),
      y: Math.max(0.01, scale.y),
      z: Math.max(0.01, scale.z)
    };

    particles.forEach(p => {
      p.size = p.baseSize * Math.max(currentScale.x, currentScale.z);
      p.velocity.set(
        p.velocity.x * currentScale.x,
        p.velocity.y * currentScale.y,
        p.velocity.z * currentScale.z
      );
      p.position.set(
        emitterPosition.x + (p.position.x - emitterPosition.x) * currentScale.x,
        emitterPosition.y + (p.position.y - emitterPosition.y) * currentScale.y,
        emitterPosition.z + (p.position.z - emitterPosition.z) * currentScale.z
      );
    });
    _updateGeometry();
  }

  function getCollisionDetected() {
    return collisionDetected;
  }

  function getCollidingObject() {
    return collidingObject;
  }

  function changeFlameColor(colorVec4) {
    uniforms.oColor.value.copy(colorVec4);
    particles.forEach(p => {
      p.color.setRGB(colorVec4.x, colorVec4.y, colorVec4.z);
      p.alpha = colorVec4.w;
    });
    _updateGeometry();
  }

  function updatePosition(newPosition) {
    emitterPosition.copy(newPosition);
    // Optionally reposition existing particles, or let them fade out naturally
  }

  return { 
    update, 
    setScale,
    points,
    getCollisionDetected,
    getCollidingObject,
    changeFlameColor,
    updatePosition
  };
}

export { getBunsenFlame };