import React, { Component } from "react";
// import "./Stepper.css";

class Stepper extends Component {
    constructor(props) {
        super(props);
        this.state = {
            currentStep: props.currentStep || 0, // Default to first step
            steps: props.steps || [
                { id: 1, title: "To separate naphthalene from the mixture" },
                { id: 2, title: "To separate sand from the mixture" },
                { id: 3, title: "To separate salt from the mixture" },
            ]
        };
    }

    componentDidUpdate(prevProps) {
        // Update the current step if it changed in the parent component
        if (prevProps.currentStep !== this.props.currentStep && this.props.currentStep !== undefined) {
            this.setState({ currentStep: this.props.currentStep });
        }
    }

    handleStepClick = (index) => {
        if (this.props.onStepChange && this.props.clickable !== false) {
            this.setState({ currentStep: index });
            this.props.onStepChange(index);
        }
    };

    getStepClassName = (index) => {
        const { currentStep } = this.state;
        if (index < currentStep) return "step-item step-completed";
        if (index === currentStep) return "step-item step-active";
        return "step-item step-inactive";
    };

     

    render() {
        const { steps, currentStep } = this.state;
        const { clickable = true } = this.props;

        return (
            <ol className="stepper-list">
                {steps.map((step, index) => (
                    <li key={step.id}>
                        <div
                            className={this.getStepClassName(index)}
                            role="alert"
                            onClick={() => clickable && this.handleStepClick(index)}
                            style={clickable ? { cursor: 'pointer' } : {}}
                        >
                            <div className="step-content">
                                <span className="step-sr-only">{step.title}</span>
                                <h3 className="step-title">{step.id}. {step.title}</h3>
                                {index < currentStep && (
                                    <svg className="step-icon" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 16 12">
                                        <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M1 5.917 5.724 10.5 15 1.5" />
                                    </svg>
                                )}
                                {index === currentStep && (
                                    <svg className="step-icon-arrow" aria-hidden="true" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 14 10">
                                        <path stroke="currentColor" strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M1 5h12m0 0L9 1m4 4L9 9" />
                                    </svg>
                                )}
                            </div>
                        </div>
                    </li>
                ))}
            </ol>
        );
    }
}

export default Stepper;