import React from 'react';
import ReactDOM from 'react-dom';
import { BrowserRouter as Router, Route, Routes } from 'react-router-dom';

/* Components */
import MainCanvas from './Components/MainCanvas';
import FlameTestComponent from './Components/FlameTestComponent';
import MainHomePage from './Components/MainHomePage';
import ArchemedisePrincipalComponent from './Components/ArchemedisePrincipalComponent';
import MetalOxideComponent from './Components/MetalOxideComponent';
import IonPrecipitate from './Components/IonPrecipitate';
import ReactionsExperiment from './Components/ReactionsExperiment';

/* Styles */
import './Styles/canvasstyle.css';
import './Styles/FlameComponent.css';
import './Styles/MainHomePage.css';
import './Styles/MetalOxideComponent.css';
import './Styles/cations.css';

ReactDOM.render(
  <Router>
    <Routes>
      <Route path="/" element={<MainHomePage />} />
      <Route path="/experiment1" element={<MainCanvas />} />
      <Route path="/experiment2" element={<FlameTestComponent />} />
      <Route path="/experiment3" element={<ArchemedisePrincipalComponent />} />
      <Route path="/experiment4" element={<MetalOxideComponent />} />
      <Route path="/experiment5" element={<IonPrecipitate />} />
      <Route path="/experiment6" element={<ReactionsExperiment />} />
    </Routes>
  </Router>,
  document.getElementById('root')
);
